import React from "react";
import { WindowStats, Particle } from "../../types";
import { formatTime } from "../../utils/calculations";

interface DebugReportProps {
  elapsedTime: number;
  destroyedBySource: number;
  destroyedByGround: number;
  particles: React.MutableRefObject<Particle[]>;
  windows: WindowStats[];
  thresholdSetting: number;
  watermarkDelay: number;
  windowWidth: number;
  resetCount: number;
  lastResetTime: number;
}

const DebugReport: React.FC<DebugReportProps> = ({
  elapsedTime,
  destroyedBySource,
  destroyedByGround,
  particles,
  windows,
  thresholdSetting,
  watermarkDelay,
  windowWidth,
  resetCount,
  lastResetTime,
}) => {
  return (
    <div style={{ fontSize: windowWidth <= 600 ? "9px" : "11px" }}>
      <h3
        style={{
          margin: "0 0 10px 0",
          color: "#3498db",
          fontSize: windowWidth <= 600 ? "10px" : "inherit",
        }}
      >
        Debug Information
      </h3>

      <div style={{ marginBottom: "15px" }}>
        <div
          style={{
            fontSize: windowWidth <= 600 ? "8px" : "10px",
            color: "#aaa",
            marginBottom: "5px",
          }}
        >
          Elapsed: {formatTime(elapsedTime)}
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "9px" : "inherit" }}>
          Destroyed by Source: {destroyedBySource}
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "9px" : "inherit" }}>
          Destroyed by Ground: {destroyedByGround}
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "9px" : "inherit" }}>
          Active Particles: {particles.current.length}
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "9px" : "inherit" }}>
          Tracked Windows: {windows.length}/40
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "9px" : "inherit" }}>
          Active Particles: {particles.current.length}
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "9px" : "inherit" }}>
          Resets: {resetCount}{" "}
          {lastResetTime > 0 &&
            `(Last: ${Math.floor((Date.now() - lastResetTime) / 1000)}s ago)`}
        </div>
      </div>

      <div style={{ fontSize: windowWidth <= 600 ? "9px" : "12px" }}>
        <div>Producer 1: Regular time (0.2s)</div>
        <div>Producer 2: Jumbled time (0.2s)</div>
        <div>Producer 3: Burst mode (2.0s)</div>
        <div
          style={{
            marginTop: "5px",
            color: "#aaa",
            fontSize: windowWidth <= 600 ? "8px" : "inherit",
          }}
        >
          Threshold: {formatTime(thresholdSetting)} | Fill: Time Group |
          Stroke: Drop Status
        </div>
      </div>
    </div>
  );
};

export default DebugReport;
