        id: "watermarking-5",
        title: "Watermarks in action",
        content: `
Flink will always _advance_ the watermark if a newer one arrives; it won’t reverse it if an older one does.


**All a watermark is doing is telling Flink “_Consider the stream complete up to this point, but not beyond._”**
`,
