      //         content:
      //           "Setting up comprehensive analysis with multiple simultaneous actions.",
      //         actions: [
      //           { type: "set-window-size-100000" },
      //           { type: "show-line-chart-set", value: "true" },
      //           { type: "hide-windows-set", value: "false" },
      //           { type: "spotlight-producer-set", producerIndex: 0 },
