      //       {
      //         id: "overlay-with-actions",
      //         title: "Overlay with Actions",
      //         content:
      //           "Showing overlay while performing other actions simultaneously.",
      //         actions: [
      //           { type: "show-overlay" },
      //           { type: "pause-set", value: "true" },
      //           { type: "highlight-closest-particle" },
      //         ],
      //         overlayContent: `# Overlay with Multiple Actions

      // This overlay is shown while other actions are executed simultaneously:

      // ## Actions Performed
      // - **Overlay Display** - This overlay is shown
      // - **Simulation Pause** - Simulation is paused for analysis
      // - **Particle Highlight** - Closest particle is highlighted

      // ## Benefits of Multiple Actions
      // - **Coordinated Effects** - Multiple changes happen together
      // - **Better UX** - No sequential delays between actions
      // - **Complex Scenarios** - Enable sophisticated interaction patterns

      // ## Use Cases
      // - **Analysis Mode** - Pause + highlight + show details
      // - **Setup Mode** - Multiple producers + settings + visualization
      // - **Demo Mode** - Show overlay + perform actions + explain

