import { SIMULATION_CONSTANTS, BLUE_SCALE } from "./constants";

export const getThresholdColor = (windowId: number): string => {
  const thresholdIndex = Math.max(0, windowId);
  const colorIndex = thresholdIndex % BLUE_SCALE.length;
  return BLUE_SCALE[colorIndex];
};

export const calculateWatermarkValue = (
  counts: [number, number, number],
  watermarkDelay: number
): number => {
  const totalCount = counts.reduce((sum, count) => sum + count, 0);
  if (totalCount === 0) return 0;

  const weightedSum = counts.reduce((sum, count, index) => {
    const weight = Math.pow(2, index); // P1=1, P2=2, P3=4
    return sum + count * weight;
  }, 0);

  return Math.min(
    weightedSum * watermarkDelay,
    SIMULATION_CONSTANTS.MAX_WATERMARK
  );
};

export const calculateDistance = (
  x1: number,
  y1: number,
  x2: number,
  y2: number
): number => {
  const dx = x2 - x1;
  const dy = y2 - y1;
  return Math.sqrt(dx * dx + dy * dy);
};

export const findClosestParticle = (
  particles: any[],
  targetX: number,
  targetY: number
): any | null => {
  if (particles.length === 0) return null;

  let closestParticle = particles[0];
  let closestDistance = calculateDistance(
    particles[0].position.x,
    particles[0].position.y,
    targetX,
    targetY
  );

  for (let i = 1; i < particles.length; i++) {
    const distance = calculateDistance(
      particles[i].position.x,
      particles[i].position.y,
      targetX,
      targetY
    );

    if (distance < closestDistance) {
      closestDistance = distance;
      closestParticle = particles[i];
    }
  }

  return closestParticle;
};

// Time formatting utility
export const formatTime = (timeMs: number): string => {
  const seconds = Math.floor(timeMs / 1000);
  const milliseconds = Math.floor((timeMs % 1000) / 100);
  return `${seconds}.${milliseconds}s`;
};

// Clock time formatting utility (starts at 12:00:00)
export const formatClockTime = (timeMs: number): string => {
  // Add 12 hours (43,200,000 ms) to start at noon
  const totalMs = timeMs + 12 * 60 * 60 * 1000;

  const hours = Math.floor(totalMs / (1000 * 60 * 60)) % 24;
  const minutes = Math.floor(totalMs / (1000 * 60)) % 60;
  const seconds = Math.floor(totalMs / 1000) % 60;
  const milliseconds = Math.floor((totalMs % 1000) / 100);

  return `${hours.toString().padStart(2, "0")}:${minutes
    .toString()
    .padStart(2, "0")}:${seconds.toString().padStart(2, "0")}.${milliseconds}`;
};

// Color utility functions
export const hexToRgba = (hex: string, alpha: number): string => {
  const r = parseInt(hex.slice(1, 3), 16);
  const g = parseInt(hex.slice(3, 5), 16);
  const b = parseInt(hex.slice(5, 7), 16);
  return `rgba(${r}, ${g}, ${b}, ${alpha})`;
};

// Jitter distribution utility
export const getLongTailJitter = (): number => {
  const rand = Math.random();

  // Balanced long-tail distribution:
  // 25% on time, 35% 0-2s, 20% 2-5s, 10% 5-10s, 10% 10-15s
  if (rand < 0.25) {
    // 25% of values: 0ms (on time)
    return 0;
  } else if (rand < 0.6) {
    // 35% of values: 0-2000ms (small delay)
    return Math.random() * 2000;
  } else if (rand < 0.8) {
    // 20% of values: 2000-5000ms (moderate delay)
    return 2000 + Math.random() * 3000;
  } else if (rand < 0.9) {
    // 10% of values: 5000-10000ms (significant delay)
    return 5000 + Math.random() * 5000;
  } else {
    // 10% of values: 10000-15000ms (very late events)
    return 10000 + Math.random() * 5000;
  }
};

// Window line width calculation
export const getLineWidth = (windowSize: number): number => {
  return Math.max(1, (windowSize / 500) * 2); // Minimum 1px width
};

// Window line height calculation
export const getLineHeight = (
  producerCounts: [number, number, number]
): number => {
  const {
    WINDOW_LINE_BASE_HEIGHT,
    WINDOW_LINE_MAX_HEIGHT,
    WINDOW_LINE_HEIGHT_PER_PARTICLE,
  } = SIMULATION_CONSTANTS;
  const totalParticles = producerCounts.reduce((sum, count) => sum + count, 0);
  return Math.min(
    WINDOW_LINE_BASE_HEIGHT + totalParticles * WINDOW_LINE_HEIGHT_PER_PARTICLE,
    WINDOW_LINE_MAX_HEIGHT
  );
};
