import React, { useState, useCallback, useEffect, useRef } from "react";
import "./App.css";
import MatterCanvas from "./components/simulation/MatterCanvas";
import ScrollController from "./components/scrollytelling/ScrollController";
import MarkdownOverlay from "./components/ui/MarkdownOverlay";
import ErrorBoundary from "./components/ErrorBoundary";

function App() {
  // State for controlling the simulation
  const [producerStates, setProducerStates] = useState([true, false, false]); // P1 on, P2 and P3 off
  const [thresholdSetting, setThresholdSetting] = useState(100000); // 100k window size
  const [watermarkDelay, setwatermarkDelay] = useState(1000);
  const [activeTab, setActiveTab] = useState("operator");
  const [showLineChart, setShowLineChart] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [hideProducers, setHideProducers] = useState([false, true, true]); // P1 visible, P2 and P3 hidden
  const [hideWindows, setHideWindows] = useState(true); // Windows hidden
  const [spotlightProducer, setSpotlightProducer] = useState<number | null>(
    null
  ); // No spotlight initially
  const [overlayVisible, setOverlayVisible] = useState(false);
  const [overlayContent, setOverlayContent] = useState("");
  const [overlayTitle, setOverlayTitle] = useState("Information");
  const [lastOverlayStepId, setLastOverlayStepId] = useState<string | null>(
    null
  );
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [currentSlide, setCurrentSlide] = useState(1);
  const matterCanvasRef = useRef<any>(null);

  // URL management for permalinking
  const updateURL = useCallback((slideNumber: number) => {
    const url = new URL(window.location.href);
    if (slideNumber === 0) {
      // Clear the slide parameter for slide 1 (first slide)
      url.searchParams.delete("slide");
    } else {
      // Set slide parameter for slides 2 and beyond
      url.searchParams.set("slide", slideNumber.toString());
    }
    window.history.replaceState({}, "", url.toString());
  }, []);

  const getSlideFromURL = useCallback(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const slideParam = urlParams.get("slide");
    return slideParam ? parseInt(slideParam, 10) : 1;
  }, []);

  // Initialize slide from URL on app load
  useEffect(() => {
    const initialSlide = getSlideFromURL();
    setCurrentSlide(initialSlide);
  }, [getSlideFromURL]);

  // Handle window resize
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
      // Reload the app when window is resized
      window.location.reload();
    };

    // Store original console.error
    const originalConsoleError = console.error;

    // Override console.error to suppress ResizeObserver errors
    console.error = (...args: any[]) => {
      const message = args.join(" ");
      if (
        message.includes(
          "ResizeObserver loop completed with undelivered notifications"
        )
      ) {
        // Suppress this specific error
        return;
      }
      // Call original console.error for other errors
      originalConsoleError.apply(console, args);
    };

    // Handle ResizeObserver errors
    const handleError = (e: ErrorEvent) => {
      if (
        e.message &&
        e.message.includes(
          "ResizeObserver loop completed with undelivered notifications"
        )
      ) {
        // This is a harmless error that can be safely ignored
        e.preventDefault();
        e.stopImmediatePropagation();
        return false;
      }
    };

    // Handle unhandled promise rejections that might contain ResizeObserver errors
    const handleUnhandledRejection = (e: PromiseRejectionEvent) => {
      if (
        e.reason &&
        e.reason.message &&
        e.reason.message.includes("ResizeObserver")
      ) {
        e.preventDefault();
        return false;
      }
    };

    // Add global error handler for all errors
    const globalErrorHandler = (e: ErrorEvent) => {
      if (e.message && e.message.includes("ResizeObserver")) {
        e.preventDefault();
        e.stopImmediatePropagation();
        return false;
      }
    };

    window.addEventListener("error", handleError);
    window.addEventListener("error", globalErrorHandler);
    window.addEventListener("unhandledrejection", handleUnhandledRejection);
    window.addEventListener("resize", handleResize);

    return () => {
      // Restore original console.error
      console.error = originalConsoleError;

      window.removeEventListener("error", handleError);
      window.removeEventListener("error", globalErrorHandler);
      window.removeEventListener(
        "unhandledrejection",
        handleUnhandledRejection
      );
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  // Callback functions for ScrollController
  const handleProducerToggle = useCallback(
    (producerIndex: number, enabled: boolean) => {
      setProducerStates((prev) => {
        const newStates = [...prev];
        newStates[producerIndex] = enabled;
        return newStates;
      });
    },
    []
  );

  const handleThresholdChange = useCallback((threshold: number) => {
    setThresholdSetting(threshold);
  }, []);

  const handlewatermarkDelayChange = useCallback((strategy: number) => {
    setwatermarkDelay(strategy);
  }, []);

  const handleTabChange = useCallback((tab: string) => {
    setActiveTab(tab);
  }, []);

  const handleShowLineChartToggle = useCallback((show: boolean) => {
    setShowLineChart(show);
  }, []);

  const handlePauseToggle = useCallback((paused: boolean) => {
    setIsPaused(paused);
  }, []);

  const handleHideProducerToggle = useCallback(
    (producerIndex: number, hidden: boolean) => {
      setHideProducers((prev) => {
        const newStates = [...prev];
        newStates[producerIndex] = hidden;
        return newStates;
      });

      // When hiding a producer, also turn it off
      if (hidden) {
        setProducerStates((prev) => {
          const newStates = [...prev];
          newStates[producerIndex] = false;
          return newStates;
        });
      }
    },
    []
  );

  const handleHideWindowsToggle = useCallback((hide: boolean) => {
    setHideWindows(hide);
  }, []);

  const handleSetWindowSizeTo100000 = useCallback(() => {
    setThresholdSetting(100000);
  }, []);

  const handleSpotlightProducerToggle = useCallback(
    (producerIndex: number) => {
      setSpotlightProducer(
        spotlightProducer === producerIndex ? null : producerIndex
      );
    },
    [spotlightProducer]
  );

  const handleHighlightClosestParticle = useCallback(() => {
    if (matterCanvasRef.current) {
      matterCanvasRef.current.highlightClosestParticle();
    }
  }, []);

  const handleResetSimulation = useCallback(() => {
    if (matterCanvasRef.current) {
      matterCanvasRef.current.resetSimulation();
    }
  }, []);

  const handleClearHighlightedParticle = useCallback(() => {
    if (matterCanvasRef.current) {
      matterCanvasRef.current.clearHighlightedParticle();
    }
  }, []);

  const handleShowOverlay = useCallback(
    (content: string, title?: string, stepId?: string) => {
      setOverlayContent(content);
      setOverlayTitle(title || "Information");
      setOverlayVisible(true);
      if (stepId) {
        setLastOverlayStepId(stepId);
      }
    },
    []
  );

  const handleHideOverlay = useCallback(() => {
    setOverlayVisible(false);
  }, []);

  const handleReopenOverlay = useCallback(() => {
    if (lastOverlayStepId) {
      setOverlayVisible(true);
    }
  }, [lastOverlayStepId]);

  return (
    <ErrorBoundary>
      <div
        className="App"
        style={{ display: "flex", height: "100vh", overflow: "hidden" }}
      >
        <div style={{ width: "66.67%", height: "100%" }}>
          <MatterCanvas
            ref={matterCanvasRef}
            producerStates={producerStates}
            thresholdSetting={thresholdSetting}
            watermarkDelay={watermarkDelay}
            activeTab={activeTab}
            showLineChart={showLineChart}
            isPaused={isPaused}
            hideProducers={hideProducers}
            hideWindows={hideWindows}
            spotlightProducer={spotlightProducer}
            overlayVisible={overlayVisible}
            overlayContent={overlayContent}
            overlayTitle={overlayTitle}
            onHighlightClosestParticle={handleHighlightClosestParticle}
            onResetSimulation={handleResetSimulation}
            onShowOverlay={handleShowOverlay}
            onHideOverlay={handleHideOverlay}
            windowWidth={windowWidth}
          />
        </div>
        <div style={{ width: "33.33%", height: "100%" }}>
          <ScrollController
            onProducerToggle={handleProducerToggle}
            onThresholdChange={handleThresholdChange}
            onwatermarkDelayChange={handlewatermarkDelayChange}
            onTabChange={handleTabChange}
            onShowLineChartToggle={handleShowLineChartToggle}
            onPauseSet={handlePauseToggle}
            onHideProducerSet={handleHideProducerToggle}
            onHideWindowsSet={handleHideWindowsToggle}
            onSpotlightProducerSet={handleSpotlightProducerToggle}
            onHighlightClosestParticle={handleHighlightClosestParticle}
            onSetWindowSizeTo100000={handleSetWindowSizeTo100000}
            onShowOverlay={handleShowOverlay}
            onHideOverlay={handleHideOverlay}
            onReopenOverlay={handleReopenOverlay}
            onResetSimulation={handleResetSimulation}
            onClearHighlightedParticle={handleClearHighlightedParticle}
            currentThreshold={thresholdSetting}
            currentwatermarkDelay={watermarkDelay}
            currentTab={activeTab}
            showLineChart={showLineChart}
            producerStates={producerStates}
            hideProducers={hideProducers}
            isPaused={isPaused}
            hideWindows={hideWindows}
            spotlightProducer={spotlightProducer}
            lastOverlayStepId={lastOverlayStepId}
            currentSlide={currentSlide}
            onSlideChange={updateURL}
          />
        </div>
        <MarkdownOverlay
          isVisible={overlayVisible}
          content={overlayContent}
          title={overlayTitle}
          onClose={handleHideOverlay}
        />
        <footer
          className="app-footer"
          role="contentinfo"
          aria-label="Site footer"
        >
          <div className="footer-accent"></div>
          <div className="footer-content">
            <div className="footer-left">
              <span>🐿️ Built with hugs and squirrels by the team at</span>
              <a
                href="https://developer.confluent.io"
                target="_blank"
                rel="noreferrer noopener"
              >
                Confluent Developer
              </a>
            </div>
            <div className="footer-right">
              <span>
                Apache and Apache Flink are trademarks of the Apache Software
                Foundation in the United States and/or other countries.
              </span>
            </div>
          </div>
        </footer>
      </div>
    </ErrorBoundary>
  );
}

export default App;
