import React from "react";
import ReactMarkdown from "react-markdown";

interface MarkdownRendererProps {
  content: string;
  className?: string;
  style?: React.CSSProperties;
}

const MarkdownRenderer: React.FC<MarkdownRendererProps> = ({
  content,
  className,
  style,
}) => {
  return (
    <div className={className} style={style}>
      <ReactMarkdown
        components={{
          // Customize heading styles
          h1: ({ children }) => (
            <h1
              style={{
                color: "#2d3436",
                fontSize: "1.5rem",
                marginBottom: "0.5rem",
                marginTop: "0",
              }}
            >
              {children}
            </h1>
          ),
          h2: ({ children }) => (
            <h2
              style={{
                color: "#00b894",
                fontSize: "1.25rem",
                marginBottom: "0.5rem",
                marginTop: "0",
              }}
            >
              {children}
            </h2>
          ),
          h3: ({ children }) => (
            <h3
              style={{
                color: "#636e72",
                fontSize: "1.1rem",
                marginBottom: "0.5rem",
                marginTop: "0",
              }}
            >
              {children}
            </h3>
          ),
          // Customize paragraph styles
          p: ({ children }) => (
            <p
              style={{
                color: "#636e72",
                lineHeight: "1.6",
                marginBottom: "1rem",
                marginTop: "0",
              }}
            >
              {children}
            </p>
          ),
          // Customize list styles
          ul: ({ children }) => (
            <ul
              style={{
                color: "#636e72",
                lineHeight: "1.6",
                marginBottom: "1rem",
                paddingLeft: "1.5rem",
              }}
            >
              {children}
            </ul>
          ),
          ol: ({ children }) => (
            <ol
              style={{
                color: "#636e72",
                lineHeight: "1.6",
                marginBottom: "1rem",
                paddingLeft: "1.5rem",
              }}
            >
              {children}
            </ol>
          ),
          // Customize code styles
          code: ({ children, className }) => {
            const isInline = !className;
            return isInline ? (
              <code
                style={{
                  backgroundColor: "#f8f9fa",
                  padding: "0.2rem 0.4rem",
                  borderRadius: "3px",
                  fontFamily: "monospace",
                  fontSize: "0.9em",
                  color: "#e74c3c",
                }}
              >
                {children}
              </code>
            ) : (
              <code
                className={className}
                style={{
                  backgroundColor: "#f8f9fa",
                  padding: "0.2rem 0.4rem",
                  borderRadius: "3px",
                  fontFamily: "monospace",
                  fontSize: "0.9em",
                  color: "#e74c3c",
                  display: "block",
                  marginBottom: "1rem",
                }}
              >
                {children}
              </code>
            );
          },
          pre: ({ children }) => (
            <pre
              style={{
                backgroundColor: "#f8f9fa",
                padding: "1rem",
                borderRadius: "5px",
                overflow: "auto",
                marginBottom: "1rem",
                border: "1px solid #e9ecef",
              }}
            >
              {children}
            </pre>
          ),
          // Customize blockquote styles
          blockquote: ({ children }) => (
            <blockquote
              style={{
                borderLeft: "4px solid #00b894",
                paddingLeft: "1rem",
                margin: "1rem 0",
                fontStyle: "italic",
                color: "#636e72",
                backgroundColor: "#f8f9fa",
                padding: "1rem",
                borderRadius: "0 5px 5px 0",
              }}
            >
              {children}
            </blockquote>
          ),
          // Customize link styles
          a: ({ children, href }) => (
            <a
              href={href}
              style={{
                color: "#00b894",
                textDecoration: "none",
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.textDecoration = "underline";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.textDecoration = "none";
              }}
            >
              {children}
            </a>
          ),
          // Customize strong/bold styles
          strong: ({ children }) => (
            <strong
              style={{
                color: "#2d3436",
                fontWeight: "bold",
              }}
            >
              {children}
            </strong>
          ),
          // Customize emphasis/italic styles
          em: ({ children }) => (
            <em
              style={{
                color: "#636e72",
                fontStyle: "italic",
              }}
            >
              {children}
            </em>
          ),
          // Customize image styles
          img: ({ src, alt, ...props }) => {
            // Handle images from public directory
            const imageSrc = src?.startsWith("/") ? src : `/${src}`;

            return (
              <img
                {...props}
                src={imageSrc}
                alt={alt}
                style={{
                  maxWidth: "100%",
                  height: "auto",
                  borderRadius: "6px",
                  margin: "1rem 0",
                  boxShadow: "0 2px 8px rgba(0, 0, 0, 0.1)",
                  display: "block",
                }}
                onError={(e) => {
                  // Fallback for missing images
                  const target = e.target as HTMLImageElement;
                  target.style.display = "none";
                  console.warn(`Image not found: ${imageSrc}`);
                }}
              />
            );
          },
        }}
      >
        {content}
      </ReactMarkdown>
    </div>
  );
};

export default MarkdownRenderer;
