import React, { useEffect, useRef } from "react";
import { WindowStats } from "../../types";
import { PRODUCER_COLORS } from "../../utils/constants";

interface LineChartProps {
  data: WindowStats[];
  width: number;
  height: number;
}

const LineChart: React.FC<LineChartProps> = ({
  data: windows,
  width,
  height,
}) => {
  const thresholdSetting = 100000; // Default value
  const elapsedTime = Date.now(); // Default value
  const producerColors = PRODUCER_COLORS; // Use the correct producer colors from constants
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const animationRef = useRef<number>();
  const prevMissedCountsRef = useRef<{
    [windowNum: number]: [number, number, number];
  }>({});
  const lastUpdateTimeRef = useRef<{
    [windowNum: number]: [number, number, number];
  }>({});

  // Chart dimensions and styling
  const chartWidth = width;
  const chartHeight = height;
  const padding = 40;
  const plotWidth = chartWidth - 2 * padding;
  const plotHeight = (chartHeight - 2 * padding) * 0.75; // 75% for main chart
  const barHeight = padding * 1.5; // Increased bar height for better visibility

  // Animation for pulsing circles
  const pulseAnimation = (timestamp: number) => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    // Clear canvas
    ctx.clearRect(0, 0, chartWidth, chartHeight);

    // Get window data and use window close time for x-axis
    const windowEntries = windows
      .map((stats, index) => {
        // Use window close time in seconds for x-axis
        const windowCloseTime = stats.windowEnd / 1000; // Convert to seconds
        return {
          windowNum: index,
          windowCloseTime,
          stats,
        };
      })
      .sort((a, b) => a.windowCloseTime - b.windowCloseTime);

    if (windowEntries.length === 0) return;

    // Calculate scales
    const minTime = windowEntries[0].windowCloseTime;
    const maxTime = windowEntries[windowEntries.length - 1].windowCloseTime;
    const xScale = plotWidth / Math.max(1, maxTime - minTime);

    const maxCount = Math.max(
      ...windowEntries.flatMap(({ stats }) => [
        stats.count[0],
        stats.count[1],
        stats.count[2],
      ])
    );
    const yScale = plotHeight / Math.max(1, maxCount);

    // Draw grid
    ctx.strokeStyle = "#333";
    ctx.lineWidth = 1;
    ctx.setLineDash([5, 5]);

    // Vertical grid lines - main chart area (fixed positions, max 6 ticks)
    const tickCount = 6;
    const tickStep = (maxTime - minTime) / (tickCount - 1);
    for (let i = 0; i < tickCount; i++) {
      const t = minTime + i * tickStep;
      const x = padding + (t - minTime) * xScale;
      ctx.beginPath();
      ctx.moveTo(x, padding);
      ctx.lineTo(x, padding + plotHeight);
      ctx.stroke();
    }

    // Horizontal grid lines - main chart area
    for (let i = 0; i <= maxCount; i += Math.max(1, Math.floor(maxCount / 5))) {
      const y = padding + plotHeight - i * yScale;
      ctx.beginPath();
      ctx.moveTo(padding, y);
      ctx.lineTo(padding + plotWidth, y);
      ctx.stroke();
    }

    // Vertical grid lines for bar area (fixed positions, max 6 ticks)
    for (let i = 0; i < tickCount; i++) {
      const t = minTime + i * tickStep;
      const x = padding + (t - minTime) * xScale;
      ctx.beginPath();
      ctx.moveTo(x, padding + plotHeight + 5);
      ctx.lineTo(x, padding + plotHeight + barHeight - 5);
      ctx.stroke();
    }

    ctx.setLineDash([]);

    // Draw axis labels
    ctx.fillStyle = "#fff";
    ctx.font = "12px monospace";
    ctx.textAlign = "center";

    // X-axis labels (time in seconds) - fixed positions, max 6 labels
    for (let i = 0; i < tickCount; i++) {
      const t = minTime + i * tickStep;
      const x = padding + (t - minTime) * xScale;
      ctx.fillText(t.toFixed(1) + "s", x, chartHeight - 10);
    }

    // Y-axis labels for main chart
    for (let i = 0; i <= maxCount; i += Math.max(1, Math.floor(maxCount / 5))) {
      const y = padding + plotHeight - i * yScale;
      ctx.fillText(i.toString(), 20, y + 4);
    }

    // Bar area label
    ctx.fillText("Missed", 20, padding + plotHeight + barHeight / 2 + 4);

    // Draw lines for each producer
    const producers = [0, 1, 2];
    producers.forEach((producerIndex) => {
      const color = producerColors[producerIndex];

      // Collect points for closed windows only
      const points: { x: number; y: number }[] = [];

      windowEntries.forEach(({ windowCloseTime, stats }) => {
        if (!stats.isActive) {
          // Only closed windows
          const x = padding + (windowCloseTime - minTime) * xScale;
          const y = padding + plotHeight - stats.count[producerIndex] * yScale;
          points.push({ x, y });
        }
      });

      // Draw line for this producer
      if (points.length > 1) {
        ctx.strokeStyle = color;
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.moveTo(points[0].x, points[0].y);

        for (let i = 1; i < points.length; i++) {
          ctx.lineTo(points[i].x, points[i].y);
        }
        ctx.stroke();
      }

      // Draw points for closed windows
      points.forEach((point) => {
        ctx.fillStyle = color;
        ctx.beginPath();
        ctx.arc(point.x, point.y, 4, 0, 2 * Math.PI);
        ctx.fill();
      });
    });

    // Draw pulsing circles for active windows
    const pulsePhase = (timestamp / 1000) % 2; // 2 second pulse cycle

    windowEntries.forEach(({ windowCloseTime, stats }) => {
      if (stats.isActive) {
        // Only active windows
        const x = padding + (windowCloseTime - minTime) * xScale;

        producers.forEach((producerIndex) => {
          const color = producerColors[producerIndex];
          const y = padding + plotHeight - stats.count[producerIndex] * yScale;

          // Pulsing effect
          const pulseSize = 4 + 2 * Math.sin(pulsePhase * Math.PI);
          const pulseAlpha = 0.3 + 0.7 * Math.sin(pulsePhase * Math.PI);

          // Draw dashed circle outline instead of filled circle
          ctx.strokeStyle = color;
          ctx.lineWidth = 2;
          ctx.setLineDash([3, 3]); // Create dashed line pattern
          ctx.globalAlpha = pulseAlpha;
          ctx.beginPath();
          ctx.arc(x, y, pulseSize, 0, 2 * Math.PI);
          ctx.stroke();
          ctx.setLineDash([]); // Reset line dash
        });

        ctx.globalAlpha = 1; // Reset alpha
      }
    });

    // Draw stacked bars for missed particles
    const barPulsePhase = (timestamp / 500) % 1; // Faster pulse for bars (0.5 second cycle)

    // Calculate max missed count for scaling
    const maxMissed = Math.max(
      ...windowEntries.flatMap(({ stats }) => [
        stats.missed[0],
        stats.missed[1],
        stats.missed[2],
      ])
    );

    const missedYScale = (barHeight - 10) / Math.max(1, maxMissed);

    // Draw window size indicators above each column
    windowEntries.forEach(({ windowCloseTime, stats }) => {
      const x = padding + (windowCloseTime - minTime) * xScale;
      const y = padding - 15; // Position above the chart area

      // Calculate circle size based on watermark delay (1px to 5px radius)
      const watermarkValue = stats.watermarkValue;
      const circleRadius =
        watermarkValue === 0
          ? 0
          : Math.min(5, 1 + ((watermarkValue - 100) / (25000 - 100)) * 4);

      if (circleRadius > 0) {
        // Draw filled white circle
        ctx.fillStyle = "white";
        ctx.beginPath();
        ctx.arc(x, y, circleRadius, 0, 2 * Math.PI);
        ctx.fill();
      } else {
        // Draw stroked circle for zero watermark
        ctx.strokeStyle = "white";
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.arc(x, y, 2, 0, 2 * Math.PI);
        ctx.stroke();
      }
    });

    windowEntries.forEach(({ windowNum, windowCloseTime, stats }) => {
      const x = padding + (windowCloseTime - minTime) * xScale;
      const barWidth = 8; // fixed width is more attractive
      const barX = x - barWidth / 2;

      // Define bar area bounds
      const barAreaTop = padding + plotHeight + 5;
      const barAreaBottom = barAreaTop + barHeight - 10; // Leave some margin
      const maxBarHeight = barAreaBottom - barAreaTop;

      let currentY = barAreaTop;
      const barSpacing = 2;

      // Check for updates in missed counts
      const prevMissed = prevMissedCountsRef.current[windowNum] || [0, 0, 0];
      const lastUpdateTime = lastUpdateTimeRef.current[windowNum] || [0, 0, 0];

      producers.forEach((producerIndex) => {
        const missedCount = stats.missed[producerIndex];

        if (missedCount > 0) {
          const color = producerColors[producerIndex];

          // Calculate bar height with proper scaling
          let barSegmentHeight = missedCount * missedYScale;

          // Ensure minimum height but don't exceed available space
          barSegmentHeight = Math.max(
            4,
            Math.min(barSegmentHeight, maxBarHeight - (currentY - barAreaTop))
          );

          // Check if we have space for this bar
          if (currentY + barSegmentHeight <= barAreaBottom) {
            // Check if this value was updated recently (within last 500ms)
            const timeSinceUpdate = timestamp - lastUpdateTime[producerIndex];
            const isRecentlyUpdated = timeSinceUpdate < 500;

            // Pulsing effect only for recently updated bars
            let pulseAlpha = 0.8; // Default alpha
            if (isRecentlyUpdated) {
              const pulseIntensity =
                0.5 + 0.5 * Math.sin(barPulsePhase * 2 * Math.PI);
              pulseAlpha = 0.6 + 0.4 * pulseIntensity;
            }

            ctx.fillStyle = color;
            ctx.globalAlpha = pulseAlpha;
            ctx.fillRect(barX, currentY, barWidth, barSegmentHeight);

            // Add border
            ctx.strokeStyle = "#fff";
            ctx.lineWidth = 1;
            ctx.strokeRect(barX, currentY, barWidth, barSegmentHeight);

            currentY += barSegmentHeight + barSpacing;
          }
        }

        // Check if missed count changed and update timestamp
        if (missedCount !== prevMissed[producerIndex]) {
          lastUpdateTimeRef.current[windowNum] = lastUpdateTimeRef.current[
            windowNum
          ] || [0, 0, 0];
          lastUpdateTimeRef.current[windowNum][producerIndex] = timestamp;
        }
      });

      // Update tracking for next frame
      prevMissedCountsRef.current[windowNum] = [...stats.missed];

      ctx.globalAlpha = 1; // Reset alpha
    });

    // Draw legend
    ctx.fillStyle = "#fff";
    ctx.font = "14px monospace";
    ctx.textAlign = "left";

    producers.forEach((producerIndex, i) => {
      const color = producerColors[producerIndex];
      const x = chartWidth - 150;
      const y = 15 + i * 20;

      // Legend line
      ctx.strokeStyle = color;
      ctx.lineWidth = 2;
      ctx.beginPath();
      ctx.moveTo(x, y);
      ctx.lineTo(x + 20, y);
      ctx.stroke();

      // Legend text
      ctx.fillStyle = "#fff";
      ctx.fillText(`Producer ${producerIndex + 1}`, x + 25, y + 4);
    });

    // Continue animation
    animationRef.current = requestAnimationFrame(pulseAnimation);
  };

  useEffect(() => {
    if (canvasRef.current) {
      animationRef.current = requestAnimationFrame(pulseAnimation);
    }

    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, [windows, thresholdSetting, elapsedTime, producerColors]);

  return (
    <div
      style={{
        background: "rgba(0,0,0,0.8)",
        padding: "10px",
        borderRadius: "5px",
        fontFamily: "monospace",
        height: "100%",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <div
        style={{
          color: "white",
          fontSize: "14px",
          marginBottom: "10px",
          textAlign: "center",
        }}
      >
        Particle Counts Over Time
      </div>
      <div
        style={{
          flex: 1,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <canvas
          ref={canvasRef}
          width={chartWidth}
          height={chartHeight}
          style={{
            border: "1px solid #444",
            borderRadius: "3px",
            maxWidth: "100%",
            maxHeight: "100%",
          }}
        />
      </div>
    </div>
  );
};

export default LineChart;
