import React from "react";

interface ProducerTooltipProps {
  show: boolean;
  x: number;
  y: number;
  producerIndex: number;
  totalProduced: number;
  totalLost: number;
  windowWidth: number;
}

const ProducerTooltip: React.FC<ProducerTooltipProps> = ({
  show,
  x,
  y,
  producerIndex,
  totalProduced,
  totalLost,
  windowWidth,
}) => {
  if (!show) return null;

  return (
    <div
      style={{
        position: "fixed",
        left: x + 10,
        top: y - 10,
        backgroundColor: "rgba(0, 0, 0, 0.9)",
        color: "white",
        padding: "8px 12px",
        borderRadius: "6px",
        fontSize: windowWidth <= 600 ? "10px" : "12px",
        fontFamily: "monospace",
        zIndex: 1000,
        pointerEvents: "none",
        whiteSpace: "nowrap",
        boxShadow: "0 2px 8px rgba(0, 0, 0, 0.3)",
        border: "1px solid rgba(255, 255, 255, 0.2)",
      }}
    >
      <div style={{ fontWeight: "bold", marginBottom: "4px" }}>
        Producer {producerIndex + 1}
      </div>
      <div>Events Produced: {totalProduced}</div>
      <div>Events Lost: {totalLost}</div>
    </div>
  );
};

export default ProducerTooltip;
