import React from "react";
import { ControlsPanelProps } from "../../types";
import { formatClockTime } from "../../utils/calculations";

const ControlsPanel: React.FC<ControlsPanelProps> = ({
  thresholdSetting,
  onThresholdChange,
  onResetSimulation,
  watermarkDelay,
  onwatermarkDelayChange,
  producerStates,
  onProducerToggle,
  isPaused,
  onPauseSet,
  hideProducers,
  onHideProducerSet,
  hideWindows,
  onHideWindowsSet,
  spotlightProducer,
  onSpotlightProducerSet,
  onHighlightClosestParticle,
  onSetWindowSizeTo100000,
  elapsedTime,
  windowWidth,
  showControlsPanel,
  onControlsPanelSet,
}) => {
  // Handler for highlight button that pauses simulation if needed
  const handleHighlightClick = () => {
    if (!isPaused) {
      onPauseSet(true); // Set to paused
    }
    onHighlightClosestParticle();
  };

  const maxWatermark = 15000;
  const minWatermark = 500;

  // If controls panel is hidden, show only a toggle button
  if (!showControlsPanel) {
    return (
      <div style={{ position: "relative", width: "100%", height: "100%" }}>
        <button
          onClick={() => onControlsPanelSet(true)}
          style={{
            position: "absolute",
            top: "10px",
            right: "10px",
            padding: "8px 12px",
            fontSize: "14px",
            backgroundColor: "#3498db",
            color: "white",
            border: "none",
            borderRadius: "4px",
            cursor: "pointer",
            zIndex: 1000,
            boxShadow: "0 2px 4px rgba(0,0,0,0.2)",
          }}
        >
          ⚙️ Show Controls
        </button>
      </div>
    );
  }

  return (
    <div style={{ position: "relative", width: "100%", height: "100%" }}>
      {/* Window Size Slider */}
      <div
        style={{
          position: "absolute",
          top: "10px",
          left: "10px",
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: "10px",
          borderRadius: "5px",
          width: windowWidth <= 600 ? "150px" : "200px",
          fontFamily: "monospace",
          zIndex: 10,
        }}
      >
        <div style={{ marginBottom: "8px" }}>
          <label htmlFor="windowSize" style={{ fontSize: "12px" }}>
            Window Size: {thresholdSetting}ms
          </label>
        </div>
        <input
          id="windowSize"
          type="range"
          min="500"
          max="5000"
          step="100"
          value={thresholdSetting}
          onChange={(e) => onThresholdChange(Number(e.target.value))}
          onMouseDown={(e) => {
            // Store the initial value when dragging starts
            (e.currentTarget as any)._initialValue = Number(
              e.currentTarget.value
            );
          }}
          onMouseUp={(e) => {
            const newValue = Number(e.currentTarget.value);
            const initialValue = (e.currentTarget as any)._initialValue;
            if (newValue !== initialValue) {
              onResetSimulation();
            }
          }}
          onKeyUp={(e) => {
            if (e.key === "Enter" || e.key === " ") {
              const newValue = Number(e.currentTarget.value);
              const initialValue =
                (e.currentTarget as any)._initialValue || thresholdSetting;
              if (newValue !== initialValue) {
                onResetSimulation();
              }
            }
          }}
          style={{ width: "150px" }}
        />
      </div>

      {/* Watermark Delay Slider */}
      <div
        style={{
          position: "absolute",
          top: "10px",
          left: windowWidth <= 600 ? "180px" : "240px",
          width: windowWidth <= 600 ? "150px" : "200px",
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: "10px",
          borderRadius: "5px",
          fontFamily: "monospace",
          zIndex: 10,
        }}
      >
        <div style={{ marginBottom: "8px" }}>
          <label htmlFor="watermarkDelay" style={{ fontSize: "12px" }}>
            Watermark Delay: {watermarkDelay}ms
          </label>
        </div>
        <input
          id="watermarkDelay"
          type="range"
          min={"0"}
          max={maxWatermark}
          step={minWatermark}
          value={watermarkDelay}
          onChange={(e) => onwatermarkDelayChange(Number(e.target.value))}
          onMouseDown={(e) => {
            // Store the initial value when dragging starts
            (e.currentTarget as any)._initialValue = Number(
              e.currentTarget.value
            );
          }}
          onMouseUp={(e) => {
            const newValue = Number(e.currentTarget.value);
            const initialValue = (e.currentTarget as any)._initialValue;
            if (newValue !== initialValue) {
              onResetSimulation();
            }
          }}
          onKeyUp={(e) => {
            if (e.key === "Enter" || e.key === " ") {
              const newValue = Number(e.currentTarget.value);
              const initialValue =
                (e.currentTarget as any)._initialValue || watermarkDelay;
              if (newValue !== initialValue) {
                onResetSimulation();
              }
            }
          }}
          style={{ width: "150px" }}
        />
      </div>

      {/* Producer Toggle Buttons */}
      <div
        style={{
          position: "absolute",
          top: "10px",
          left: windowWidth <= 600 ? "350px" : "460px",
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: "10px",
          borderRadius: "5px",
          fontFamily: "monospace",
          zIndex: 10,
        }}
      >
        <div style={{ marginBottom: "8px" }}>
          <label style={{ fontSize: "12px" }}>Producers</label>
        </div>
        <div
          style={{
            display: "flex",
            gap: windowWidth <= 600 ? "4px" : "8px",
            flexWrap: windowWidth <= 600 ? "wrap" : "nowrap",
          }}
        >
          <button
            onClick={() => {
              const currentState = producerStates[0];
              const desiredState = !currentState;
              if (currentState !== desiredState) {
                onProducerToggle(0, desiredState);
              }
              if (desiredState && hideProducers[0]) {
                onHideProducerSet(0, false);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: producerStates[0] ? "#27ae60" : "#e74c3c",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            P1: {producerStates[0] ? "ON" : "OFF"}
          </button>
          <button
            onClick={() => {
              const currentVisibility = hideProducers[0];
              const shouldBeVisible = !currentVisibility;
              if (currentVisibility !== shouldBeVisible) {
                onHideProducerSet(0, !shouldBeVisible);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: hideProducers[0] ? "#e74c3c" : "#27ae60",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {hideProducers[0] ? "👁️" : "🙈"}
          </button>
          <button
            onClick={() => {
              const currentSpotlight = spotlightProducer;
              const desiredSpotlight = currentSpotlight === 0 ? null : 0;
              if (currentSpotlight !== desiredSpotlight) {
                onSpotlightProducerSet(desiredSpotlight);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: spotlightProducer === 0 ? "#f39c12" : "#95a5a6",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {spotlightProducer === 0 ? "🔦" : "💡"}
          </button>
          <button
            onClick={() => {
              const currentState = producerStates[1];
              const desiredState = !currentState;
              if (currentState !== desiredState) {
                onProducerToggle(1, desiredState);
              }
              if (desiredState && hideProducers[1]) {
                onHideProducerSet(1, false);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: producerStates[1] ? "#27ae60" : "#e74c3c",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            P2: {producerStates[1] ? "ON" : "OFF"}
          </button>
          <button
            onClick={() => {
              const currentVisibility = hideProducers[1];
              const shouldBeVisible = !currentVisibility;
              if (currentVisibility !== shouldBeVisible) {
                onHideProducerSet(1, !shouldBeVisible);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: hideProducers[1] ? "#e74c3c" : "#27ae60",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {hideProducers[1] ? "👁️" : "🙈"}
          </button>
          <button
            onClick={() => {
              const currentSpotlight = spotlightProducer;
              const desiredSpotlight = currentSpotlight === 1 ? null : 1;
              if (currentSpotlight !== desiredSpotlight) {
                onSpotlightProducerSet(desiredSpotlight);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: spotlightProducer === 1 ? "#f39c12" : "#95a5a6",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {spotlightProducer === 1 ? "🔦" : "💡"}
          </button>
          <button
            onClick={() => {
              const currentState = producerStates[2];
              const desiredState = !currentState;
              if (currentState !== desiredState) {
                onProducerToggle(2, desiredState);
              }
              if (desiredState && hideProducers[2]) {
                onHideProducerSet(2, false);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: producerStates[2] ? "#27ae60" : "#e74c3c",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            P3: {producerStates[2] ? "ON" : "OFF"}
          </button>
          <button
            onClick={() => {
              const currentVisibility = hideProducers[2];
              const shouldBeVisible = !currentVisibility;
              if (currentVisibility !== shouldBeVisible) {
                onHideProducerSet(2, !shouldBeVisible);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: hideProducers[2] ? "#e74c3c" : "#27ae60",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {hideProducers[2] ? "👁️" : "🙈"}
          </button>
          <button
            onClick={() => {
              const currentSpotlight = spotlightProducer;
              const desiredSpotlight = currentSpotlight === 2 ? null : 2;
              if (currentSpotlight !== desiredSpotlight) {
                onSpotlightProducerSet(desiredSpotlight);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: spotlightProducer === 2 ? "#f39c12" : "#95a5a6",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {spotlightProducer === 2 ? "🔦" : "💡"}
          </button>
          <button
            onClick={() => {
              const currentState = isPaused;
              const desiredState = !currentState;
              if (currentState !== desiredState) {
                onPauseSet(desiredState);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: isPaused ? "#e74c3c" : "#27ae60",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {isPaused ? "▶️" : "⏸️"}
          </button>
          <button
            onClick={() => {
              const currentState = hideWindows;
              const desiredState = !currentState;
              if (currentState !== desiredState) {
                onHideWindowsSet(desiredState);
              }
            }}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: hideWindows ? "#e74c3c" : "#27ae60",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            {hideWindows ? "📊" : "📈"}
          </button>
          <button
            onClick={onSetWindowSizeTo100000}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: "#3498db",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            100k
          </button>
          <button
            onClick={handleHighlightClick}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: "#f39c12",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            🎯
          </button>
          <button
            onClick={() => onControlsPanelSet(false)}
            style={{
              padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
              backgroundColor: "#9b59b6",
              color: "white",
              border: "none",
              borderRadius: "3px",
              cursor: "pointer",
            }}
          >
            ⚙️
          </button>
        </div>
      </div>
      <div
        style={{
          position: "fixed",
          top: "10px",
          left: "10px",
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: "10px",
          borderRadius: "5px",
          fontFamily: "monospace",
          zIndex: 10,
        }}
      >
        <div style={{ marginBottom: "8px" }}>
          <label style={{ fontSize: "12px" }}>Time at Producer</label>
        </div>
        <div style={{ fontSize: windowWidth <= 600 ? "10px" : "12px" }}>
          {formatClockTime(elapsedTime)}
        </div>
      </div>
    </div>
  );
};

export default ControlsPanel;
