import React from "react";
import { WindowStats } from "../../types";
import { PRODUCER_COLORS } from "../../utils/constants";

interface AnalystReportProps {
  windows: WindowStats[];
  windowWidth: number;
}

const AnalystReport: React.FC<AnalystReportProps> = ({
  windows,
  windowWidth,
}) => {
  // Calculate report accuracy for each producer
  const producerAccuracy = [0, 1, 2].map((producerIndex) => {
    const closedWindows = windows.filter((stats) => !stats.isActive);

    // Calculate accuracy for each window
    const windowAccuracies = closedWindows.map((stats) => {
      const counted = stats.count[producerIndex];
      const missed = stats.missed[producerIndex];
      const total = counted + missed;

      // Avoid division by zero
      if (total === 0) return 100; // If no particles were delivered, consider it 100% accurate

      return (counted / total) * 100;
    });

    // Find the minimum accuracy (worst case)
    const reportAccuracy =
      windowAccuracies.length > 0 ? Math.min(...windowAccuracies) : 100;

    // Get some additional statistics
    const totalCounted = closedWindows.reduce(
      (sum, stats) => sum + stats.count[producerIndex],
      0
    );
    const totalMissed = closedWindows.reduce(
      (sum, stats) => sum + stats.missed[producerIndex],
      0
    );
    const totalDelivered = totalCounted + totalMissed;

    return {
      producerIndex,
      reportAccuracy,
      totalCounted,
      totalMissed,
      totalDelivered,
      windowCount: closedWindows.length,
      windowAccuracies,
    };
  });

  return (
    <div style={{ fontSize: windowWidth <= 600 ? "9px" : "11px" }}>
      <h3
        style={{
          margin: "0 0 10px 0",
          color: "#3498db",
          fontSize: windowWidth <= 600 ? "10px" : "inherit",
        }}
      >
        Report Accuracy Analysis
      </h3>

      {producerAccuracy.map((accuracy) => (
        <div key={accuracy.producerIndex} style={{ marginBottom: "15px" }}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginBottom: "5px",
              color: PRODUCER_COLORS[accuracy.producerIndex],
            }}
          >
            <div
              style={{
                width: "12px",
                height: "12px",
                backgroundColor: PRODUCER_COLORS[accuracy.producerIndex],
                marginRight: "8px",
                borderRadius: "2px",
              }}
            />
            <strong>Producer {accuracy.producerIndex + 1}</strong>
          </div>

          <div
            style={{
              marginLeft: "20px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
            }}
          >
            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Report Accuracy: </span>
              <span
                style={{
                  color:
                    accuracy.reportAccuracy >= 90
                      ? "#27ae60"
                      : accuracy.reportAccuracy >= 70
                      ? "#f39c12"
                      : "#e74c3c",
                  fontWeight: "bold",
                }}
              >
                {accuracy.reportAccuracy.toFixed(1)}%
              </span>
            </div>

            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Total Particles: </span>
              <span>
                {accuracy.totalDelivered} delivered, {accuracy.totalCounted}{" "}
                counted, {accuracy.totalMissed} missed
              </span>
            </div>

            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Success Rate: </span>
              <span>
                {accuracy.totalDelivered > 0
                  ? (
                      (accuracy.totalCounted / accuracy.totalDelivered) *
                      100
                    ).toFixed(1)
                  : "100"}
                %
              </span>
            </div>

            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Windows Analyzed: </span>
              <span>{accuracy.windowCount}</span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default AnalystReport;
