import React, {
  useCallback,
  useEffect,
  useRef,
  useState,
  useImperativeHandle,
  forwardRef,
} from "react";
import {
  Engine,
  Render,
  Runner,
  Bodies,
  Composite,
  Body,
  Vector,
  Events,
} from "matter-js";
import LineChart from "./LineChart";
import ControlsPanel from "./ControlsPanel";
import ParticleTooltip from "../ui/ParticleTooltip";
import WindowTooltip from "../ui/WindowTooltip";
import ProducerTooltip from "../ui/ProducerTooltip";
import OperatorReport from "../reports/OperatorReport";
import AnalystReport from "../reports/AnalystReport";
import DebugReport from "../reports/DebugReport";
import {
  SimulationConfig,
  SimulationCallbacks,
  WindowStats,
  Particle,
} from "../../types";
import {
  SIMULATION_CONSTANTS,
  PRODUCER_COLORS,
  UI_CONSTANTS,
} from "../../utils/constants";
import {
  formatClockTime,
  hexToRgba,
  getLongTailJitter,
  getThresholdColor,
} from "../../utils/calculations";

interface MatterCanvasProps extends SimulationConfig, SimulationCallbacks {
  windowWidth: number;
}

const MatterCanvas = forwardRef<any, MatterCanvasProps>(
  (
    {
      producerStates: externalProducerStates,
      thresholdSetting: externalThresholdSetting,
      watermarkDelay: externalwatermarkDelay,
      activeTab: externalActiveTab,
      showLineChart: externalShowLineChart,
      isPaused: externalIsPaused,
      hideProducers: externalHideProducers,
      hideWindows: externalHideWindows,
      spotlightProducer: externalSpotlightProducer,
      onHighlightClosestParticle,
      onResetSimulation,
      windowWidth,
    },
    ref
  ) => {
    const matterRef = useRef(null);
    const engine = React.useMemo(() => Engine.create(), []);
    const [destroyedBySource, setDestroyedBySource] = useState(0);
    const [destroyedByGround, setDestroyedByGround] = useState(0);
    const [thresholdSetting, setThresholdSetting] = useState(
      SIMULATION_CONSTANTS.MIN_WATERMARK * 4
    );
    const [watermarkDelay, setwatermarkDelay] = useState(5000);
    const watermarkDelayRef = useRef(watermarkDelay);
    const [elapsedTime, setElapsedTime] = useState(0);
    const [producerStates, setProducerStates] = useState(
      externalProducerStates || [true, true, true]
    ); // [Producer1, Producer2, Producer3]
    const [showLineChart, setShowLineChart] = useState(
      externalShowLineChart !== undefined ? externalShowLineChart : true
    ); // Toggle between line chart and table
    const [activeTab, setActiveTab] = useState(externalActiveTab || "operator"); // Active tab for reports
    const [isPaused, setIsPaused] = useState(externalIsPaused || false); // Pause state
    const isPausedRef = useRef(isPaused);
    const [hideProducers, setHideProducers] = useState(
      externalHideProducers || [false, false, false]
    );
    const [hideWindows, setHideWindows] = useState(
      externalHideWindows || false
    );
    const [spotlightProducer, setSpotlightProducer] = useState<number | null>(
      externalSpotlightProducer || null
    );
    const [highlightedParticle, setHighlightedParticle] =
      useState<Particle | null>(null);
    const [groundPulseTimeout, setGroundPulseTimeout] =
      useState<NodeJS.Timeout | null>(null);
    const [collectorTime, setCollectorTime] = useState(0); // Latest event time received by collector (Flink-like behavior)
    const [showControlsPanel, setShowControlsPanel] = useState(false); // Controls panel visibility
    const lastSpawnTime = useRef<number[]>([0, 0, 0]); // Track last spawn time for each producer

    // Scene dimensions - responsive
    // Account for the fact that MatterCanvas now takes up 2/3 of the screen width
    const canvasWidth = Math.floor(
      (windowWidth * parseFloat(UI_CONSTANTS.SIMULATION_PANEL_WIDTH)) / 100
    );
    const sceneWidth = Math.min(
      canvasWidth - UI_CONSTANTS.SCENE_PADDING,
      UI_CONSTANTS.SCENE_MAX_WIDTH
    );
    const attactorPosition = sceneWidth - SIMULATION_CONSTANTS.ATTRACTOR_WIDTH;
    const sceneHeight = UI_CONSTANTS.SCENE_HEIGHT;

    // Sync with external props
    useEffect(() => {
      if (externalProducerStates !== undefined) {
        setProducerStates(externalProducerStates);
      }
    }, [externalProducerStates]);

    useEffect(() => {
      if (externalThresholdSetting !== undefined) {
        setThresholdSetting(externalThresholdSetting);
      }
    }, [externalThresholdSetting]);

    useEffect(() => {
      if (externalwatermarkDelay !== undefined) {
        setwatermarkDelay(externalwatermarkDelay);
      }
    }, [externalwatermarkDelay]);

    // Update ref whenever watermarkDelay changes
    useEffect(() => {
      watermarkDelayRef.current = watermarkDelay;
    }, [watermarkDelay]);

    useEffect(() => {
      if (externalActiveTab !== undefined) {
        setActiveTab(externalActiveTab);
      }
    }, [externalActiveTab]);

    useEffect(() => {
      if (externalShowLineChart !== undefined) {
        setShowLineChart(externalShowLineChart);
      }
    }, [externalShowLineChart]);

    useEffect(() => {
      if (externalIsPaused !== undefined) {
        setIsPaused(externalIsPaused);
      }
    }, [externalIsPaused]);

    useEffect(() => {
      if (externalHideProducers !== undefined) {
        setHideProducers(externalHideProducers);
      }
    }, [externalHideProducers]);

    useEffect(() => {
      if (externalHideWindows !== undefined) {
        setHideWindows(externalHideWindows);
      }
    }, [externalHideWindows]);

    useEffect(() => {
      if (externalSpotlightProducer !== undefined) {
        setSpotlightProducer(externalSpotlightProducer);
      }
    }, [externalSpotlightProducer]);

    // Windows array - the new window management system
    const [windows, setWindows] = useState<WindowStats[]>([]);
    const windowsRef = useRef<WindowStats[]>([]);

    // Tooltip state for paused particles
    const [tooltipData, setTooltipData] = useState<{
      show: boolean;
      x: number;
      y: number;
      particle: Particle | null;
    }>({
      show: false,
      x: 0,
      y: 0,
      particle: null,
    });
    const [windowLines, setWindowLines] = useState<
      Array<{
        id: number;
        windowId: number;
        color: string;
        x: number;
        width: number;
        isActive: boolean;
        pulseStartTime?: number;
        windowStart: number;
        windowEnd: number;
        watermarkValue: number;
        producerCounts: [number, number, number];
        isHighlighted?: boolean;
        isDeemphasized?: boolean;
      }>
    >([]);

    // Track which window lines are currently pulsing
    const [pulsingWindowIds, setPulsingWindowIds] = useState<Set<number>>(
      new Set()
    );

    // Reset tracking
    const [resetCount, setResetCount] = useState(0);
    const [lastResetTime, setLastResetTime] = useState(0);

    // Track active timeouts for pausing
    const activeTimeouts = useRef<Set<NodeJS.Timeout>>(new Set());

    // Track which window is being hovered for highlighting (both particles and windows)
    const [hoveredWindowId, setHoveredWindowId] = useState<number | null>(null);

    // Track particles that are in cleanup state (hit attractor or ground)
    const particlesInCleanup = useRef<Set<Particle>>(new Set());

    // Track particles that are queued for cleanup (rejected particles)
    const particlesQueuedForCleanup = useRef<Set<Particle>>(new Set());

    // Track pause state for spawn interval access

    // Track elapsed time for collision detection
    const elapsedTimeRef = useRef(elapsedTime);

    // Tooltip state for windows
    const [windowTooltipData, setWindowTooltipData] = useState<{
      show: boolean;
      x: number;
      y: number;
      windowId: number;
      windowStart: number;
      windowEnd: number;
      watermarkValue: number;
      producerCounts: [number, number, number];
    }>({
      show: false,
      x: 0,
      y: 0,
      windowId: -1,
      windowStart: 0,
      windowEnd: 0,
      watermarkValue: 0,
      producerCounts: [0, 0, 0],
    });

    // Tooltip state for producers
    const [producerTooltipData, setProducerTooltipData] = useState<{
      show: boolean;
      x: number;
      y: number;
      producerIndex: number;
      totalProduced: number;
      totalLost: number;
    }>({
      show: false,
      x: 0,
      y: 0,
      producerIndex: -1,
      totalProduced: 0,
      totalLost: 0,
    });

    // Reset function to clear particles and reset state while preserving static bodies
    const resetSimulation = useCallback(() => {
      // Clear only particles, not static bodies (producers, ground, attractor)
      particles.current.forEach((particle) => {
        if (Composite.allBodies(engine.world).includes(particle)) {
          Composite.remove(engine.world, particle);
        }
      });
      particles.current = [];

      // Clear cleanup tracking
      particlesInCleanup.current.clear();
      particlesQueuedForCleanup.current.clear();

      // Reset window tracking
      setWindowLines([]);

      // Reset counters
      setDestroyedBySource(0);
      setDestroyedByGround(0);
      setElapsedTime(0);
      setLastResetTime(Date.now());
      setResetCount((prev) => {
        const newCount = prev + 1;
        return newCount;
      });

      // Reset windows
      setWindows([]);

      // Reset burst timing
      lastBurstTime.current = 0;
      currentTimeRef.current = 0;

      // Reset spawn timing
      lastSpawnTime.current = [0, 0, 0];

      // Reset collector time
      setCollectorTime(0);

      // Clear all active timeouts
      activeTimeouts.current.forEach((timeout) => {
        clearTimeout(timeout);
      });
      activeTimeouts.current.clear();

      // Clear ground pulse timeout
      if (groundPulseTimeout) {
        clearTimeout(groundPulseTimeout);
        setGroundPulseTimeout(null);
      }

      // Reset tooltip states
      setTooltipData({
        show: false,
        x: 0,
        y: 0,
        particle: null,
      });
      setWindowTooltipData({
        show: false,
        x: 0,
        y: 0,
        windowId: -1,
        windowStart: 0,
        windowEnd: 0,
        watermarkValue: 0,
        producerCounts: [0, 0, 0],
      });
      setProducerTooltipData({
        show: false,
        x: 0,
        y: 0,
        producerIndex: -1,
        totalProduced: 0,
        totalLost: 0,
      });
      setHoveredWindowId(null);
    }, [engine, groundPulseTimeout]);

    // Callback functions for external control
    // Helper function to update missed count for a window
    const updateMissedCount = useCallback(
      (windowId: number, producerId: number) => {
        setWindows((prevWindows) => {
          const updated = [...prevWindows];
          const windowIndex = updated.findIndex((w) => w.windowID === windowId);
          if (windowIndex >= 0) {
            const windowToUpdate = updated[windowIndex];
            const newMissed = [...windowToUpdate.missed] as [
              number,
              number,
              number
            ];
            newMissed[producerId] += 1;
            updated[windowIndex] = {
              ...windowToUpdate,
              missed: newMissed,
            };
          }
          return updated;
        });
      },
      []
    );

    const handleHighlightClosestParticle = useCallback(() => {
      // Clear any existing highlight first
      setHighlightedParticle(null);

      if (particles.current.length === 0) return;
      const screenCenterX = sceneWidth / 2;
      const screenCenterY = sceneHeight / 2;
      let closestParticle: Particle | null = null;
      let closestDistance = Infinity;
      particles.current.forEach((particle) => {
        const dx = particle.position.x - screenCenterX;
        const dy = particle.position.y - screenCenterY;
        const distance = Math.sqrt(dx * dx + dy * dy);
        if (distance < closestDistance) {
          closestDistance = distance;
          closestParticle = particle;
        }
      });

      if (closestParticle) {
        setHighlightedParticle(closestParticle);

        // Show tooltip for the highlighted particle
        // Convert particle world coordinates to screen coordinates
        const canvasRect = matterRef.current?.getBoundingClientRect();
        if (canvasRect) {
          setTooltipData({
            show: true,
            x: canvasRect.left + closestParticle.position.x,
            y: canvasRect.top + closestParticle.position.y,
            particle: closestParticle,
          });
        }
      }

      // Clear any existing hover effects when highlighting
      setHoveredWindowId(null);
    }, [sceneWidth, sceneHeight]);

    // Expose resetSimulation, highlightClosestParticle, and clearHighlightedParticle functions to parent component
    useImperativeHandle(
      ref,
      () => ({
        resetSimulation,
        highlightClosestParticle: handleHighlightClosestParticle,
        clearHighlightedParticle: () => setHighlightedParticle(null),
      }),
      [resetSimulation, handleHighlightClosestParticle]
    );

    const handleSpotlightProducerSet = useCallback(
      (producerIndex: number | null) => {
        setSpotlightProducer(producerIndex);
      },
      []
    );

    const handleHideProducerSet = useCallback(
      (producerIndex: number, hidden: boolean) => {
        setHideProducers((prev) => {
          const newStates = [...prev];
          newStates[producerIndex] = hidden;
          return newStates;
        });

        // When hiding a producer, also turn it off
        if (hidden) {
          setProducerStates((prev) => {
            const newStates = [...prev];
            newStates[producerIndex] = false;
            return newStates;
          });
        }
      },
      []
    );

    // Update producer visibility when hideProducers changes
    useEffect(() => {
      if (!producers.current || !engine) return;

      producers.current.forEach((producer, index) => {
        if (producer.render) {
          if (hideProducers[index]) {
            // Hide producer by making it transparent and very small
            producer.render.opacity = 0;
            producer.render.fillStyle = "transparent";
            producer.render.strokeStyle = "transparent";
          } else {
            // Show producer with normal appearance
            producer.render.opacity = 1;
            producer.render.fillStyle = PRODUCER_COLORS[index];
            producer.render.strokeStyle = "#000";
          }
        }
      });
    }, [hideProducers, engine]);

    const handleHideWindowsSet = useCallback((hidden: boolean) => {
      setHideWindows(hidden);
    }, []);

    const handleSetWindowSizeTo100000 = useCallback(() => {
      setThresholdSetting(100000);
      resetSimulation();
    }, [resetSimulation]);

    const handleControlsPanelSet = useCallback((show: boolean) => {
      setShowControlsPanel(show);
    }, []);

    const handlePauseSet = useCallback((paused: boolean) => {
      setIsPaused(paused);
      isPausedRef.current = paused;

      // Clear spotlighting when unpausing (playing)
      if (!paused) {
        setSpotlightProducer(null);
        setHighlightedParticle(null);
      }
    }, []);

    // Update ref when producer states change
    useEffect(() => {
      producerStatesRef.current = producerStates;
    }, [producerStates]);

    // Update ref when isPaused changes
    useEffect(() => {
      isPausedRef.current = isPaused;
    }, [isPaused]);

    // Update ref when threshold setting changes
    useEffect(() => {
      thresholdSettingRef.current = thresholdSetting;
    }, [thresholdSetting]);

    // Update ref when elapsed time changes
    useEffect(() => {
      elapsedTimeRef.current = elapsedTime;
    }, [elapsedTime]);

    // Update windows ref when windows state changes
    useEffect(() => {
      windowsRef.current = windows;
    }, [windows]);

    // Handle pause functionality
    useEffect(() => {
      // Update ref for spawn interval access
      isPausedRef.current = isPaused;

      if (isPaused) {
        // Freeze all particles
        particles.current.forEach((particle) => {
          Body.setVelocity(particle, Vector.create(0, 0));
          Body.setStatic(particle, true);
        });

        // Clear all active timeouts
        activeTimeouts.current.forEach((timeout) => {
          clearTimeout(timeout);
        });
        activeTimeouts.current.clear();
      } else {
        // Unfreeze all particles
        particles.current.forEach((particle) => {
          Body.setStatic(particle, false);
          // Give a small downward velocity to resume gravity
          Body.setVelocity(particle, Vector.create(0, 1));
        });

        // Restart cleanup timeouts for particles that were in cleanup state
        particlesInCleanup.current.forEach((particle) => {
          if (Composite.allBodies(engine.world).includes(particle)) {
            // Determine cleanup timeout based on particle state
            // All particles get 2 seconds cleanup timeout
            const cleanupTimeout = 2000;

            const timeout = setTimeout(() => {
              if (Composite.allBodies(engine.world).includes(particle)) {
                Composite.remove(engine.world, particle);

                // Update appropriate counters and stats
                setDestroyedByGround((prev) => prev + 1);

                // Remove from cleanup tracking
                particlesInCleanup.current.delete(particle);
              }
            }, cleanupTimeout);

            activeTimeouts.current.add(timeout);
          }
        });
      }
    }, [isPaused, engine.world]);

    // Cleanup ground pulse timeout on unmount
    useEffect(() => {
      return () => {
        if (groundPulseTimeout) {
          clearTimeout(groundPulseTimeout);
        }
      };
    }, [groundPulseTimeout]);

    // Check for too many willDrop particles every 10 seconds
    useEffect(() => {
      const checkInterval = setInterval(() => {
        // Skip reset check if simulation is paused
        if (isPaused) {
          return;
        }

        const totalParticles = particles.current.length;

        // Reset if too many total particles
        if (totalParticles > 200) {
          resetSimulation();
        }

        // Clean up old particles (older than 15 seconds)
        const currentTime = Date.now();
        particles.current.forEach((particle) => {
          if (currentTime - particle.createdAt > 15000) {
            if (Composite.allBodies(engine.world).includes(particle)) {
              Composite.remove(engine.world, particle);
            }
          }
        });
        particles.current = particles.current.filter(
          (particle) => currentTime - particle.createdAt <= 15000
        );
      }, 10000); // Check every 10 seconds instead of 5

      return () => clearInterval(checkInterval);
    }, [resetSimulation, isPaused, engine]);

    // Mouse event handling for tooltips - only when paused
    useEffect(() => {
      if (!windowLinesCanvasRef.current) {
        return;
      }

      const canvas = windowLinesCanvasRef.current;
      let mouseMoveHandler: ((event: MouseEvent) => void) | null = null;
      let mouseLeaveHandler: (() => void) | null = null;

      if (isPaused) {
        mouseMoveHandler = (event: MouseEvent) => {
          const rect = canvas.getBoundingClientRect();
          const mouseX = event.clientX - rect.left;
          const mouseY = event.clientY - rect.top;

          // Check if mouse is over any producer circle
          let hoveredProducerIndex = -1;
          producers.current.forEach((producer, index) => {
            const distance = Math.sqrt(
              Math.pow(producer.position.x - mouseX, 2) +
                Math.pow(producer.position.y - mouseY, 2)
            );

            if (distance <= 25) {
              // 25 is the producer radius
              hoveredProducerIndex = index;
            }
          });

          if (hoveredProducerIndex !== -1) {
            // Calculate total produced and lost for this producer
            const totalProduced = windows.reduce(
              (sum, window) => sum + window.count[hoveredProducerIndex],
              0
            );
            const totalLost = windows.reduce(
              (sum, window) => sum + window.missed[hoveredProducerIndex],
              0
            );

            setProducerTooltipData({
              show: true,
              x: event.clientX,
              y: event.clientY,
              producerIndex: hoveredProducerIndex,
              totalProduced,
              totalLost,
            });
          } else {
            setProducerTooltipData((prev) => ({ ...prev, show: false }));
          }

          // Check if mouse is over any window line
          const hoveredLine = windowLines.find((line) => {
            const lineLeft = line.x - line.width / 2;
            const lineRight = line.x + line.width / 2;
            const lineTop = 125;
            const lineBottom = 475;

            return (
              mouseX >= lineLeft &&
              mouseX <= lineRight &&
              mouseY >= lineTop &&
              mouseY <= lineBottom
            );
          });

          if (hoveredLine) {
            setWindowTooltipData({
              show: true,
              x: event.clientX,
              y: event.clientY,
              windowId: hoveredLine.windowId,
              windowStart: hoveredLine.windowStart,
              windowEnd: hoveredLine.windowEnd,
              watermarkValue: hoveredLine.watermarkValue,
              producerCounts: hoveredLine.producerCounts,
            });

            // Set the hovered window for highlighting
            setHoveredWindowId(hoveredLine.windowId);

            // Clear particle tooltip when hovering over window
            setTooltipData((prev) => ({ ...prev, show: false }));
          } else {
            setWindowTooltipData((prev) => ({ ...prev, show: false }));

            // Only check for particle hover if not hovering over a window
            // Find the closest particle within 20px
            let closestParticle: Particle | null = null;
            let closestDistance = Infinity;

            particles.current.forEach((particle) => {
              const distance = Math.sqrt(
                Math.pow(particle.position.x - mouseX, 2) +
                  Math.pow(particle.position.y - mouseY, 2)
              );

              if (distance < 20 && distance < closestDistance) {
                closestDistance = distance;
                closestParticle = particle;
              }
            });

            if (closestParticle) {
              setTooltipData({
                show: true,
                x: event.clientX,
                y: event.clientY,
                particle: closestParticle,
              });

              // Set the hovered window ID for particle highlighting
              setHoveredWindowId(closestParticle.windowId);
            } else {
              setTooltipData((prev) => ({ ...prev, show: false }));
              // Clear hovered window when not hovering over any particle or window
              setHoveredWindowId(null);
            }
          }
        };

        mouseLeaveHandler = () => {
          setTooltipData((prev) => ({ ...prev, show: false }));
          setProducerTooltipData((prev) => ({ ...prev, show: false }));
          setWindowTooltipData((prev) => ({ ...prev, show: false }));
          setHoveredWindowId(null);
        };

        canvas.addEventListener("mousemove", mouseMoveHandler);
        canvas.addEventListener("mouseleave", mouseLeaveHandler);

        // Handle clicks on producers to toggle them on/off, or pause/unpause if clicking elsewhere
        const clickHandler = (e: MouseEvent) => {
          const rect = canvas.getBoundingClientRect();
          const clickX = e.clientX - rect.left;
          const clickY = e.clientY - rect.top;

          let clickedOnProducer = false;

          // Check if click is on any producer circle
          producers.current.forEach((producer, index) => {
            const distance = Math.sqrt(
              Math.pow(producer.position.x - clickX, 2) +
                Math.pow(producer.position.y - clickY, 2)
            );

            if (distance <= 25) {
              clickedOnProducer = true;
              // 25 is the producer radius - toggle this producer
              setProducerStates((prev) => {
                const newStates = [...prev];
                newStates[index] = !newStates[index];
                return newStates;
              });
            }
          });

          // If not clicking on a producer, toggle pause/unpause
          if (!clickedOnProducer) {
            handlePauseSet(!isPausedRef.current);
          }
        };

        canvas.addEventListener("click", clickHandler);

        // Store click handler for cleanup
        (canvas as any)._clickHandler = clickHandler;
      } else {
        // Hide tooltips when unpaused
        setTooltipData((prev) => ({ ...prev, show: false }));
        setProducerTooltipData((prev) => ({ ...prev, show: false }));
        setWindowTooltipData((prev) => ({ ...prev, show: false }));
        setHoveredWindowId(null);
      }

      // Cleanup function
      return () => {
        if (mouseMoveHandler) {
          canvas.removeEventListener("mousemove", mouseMoveHandler);
        }
        if (mouseLeaveHandler) {
          canvas.removeEventListener("mouseleave", mouseLeaveHandler);
        }
        // Clean up click handler
        if ((canvas as any)._clickHandler) {
          canvas.removeEventListener("click", (canvas as any)._clickHandler);
          delete (canvas as any)._clickHandler;
        }
      };
    }, [isPaused, windows]);

    // Burst timing for Producer 3
    const lastBurstTime = useRef<number>(0);
    const currentTimeRef = useRef<number>(0);
    const producerStatesRef = useRef<boolean[]>([true, true, true]);
    const thresholdSettingRef = useRef<number>(thresholdSetting);
    const particleRadius = SIMULATION_CONSTANTS.PARTICLE_RADIUS;

    // Function to update particle styling based on hovered window and highlighted particle
    const updateParticleStyling = useCallback(() => {
      const hasHighlightedParticle = highlightedParticle !== null;

      particles.current.forEach((particle) => {
        if (particle.render) {
          if (highlightedParticle && particle === highlightedParticle) {
            // Highlighted particle - make it bright and glowing
            particle.render.opacity = 1;
            particle.render.filter =
              "drop-shadow(0 0 15px rgba(255, 255, 0, 1)) drop-shadow(0 0 30px rgba(255, 255, 0, 0.8))";
          } else if (hasHighlightedParticle) {
            // Dim other particles when one is highlighted
            particle.render.opacity = 0.1;
            particle.render.filter = "none";
          } else if (isPaused && hoveredWindowId !== null) {
            // Apply hover effect to particles in the hovered window
            if (particle.windowId === hoveredWindowId) {
              particle.render.opacity = 1;
              particle.render.filter =
                "drop-shadow(0 0 10px rgba(255, 255, 255, 0.8))";
            } else {
              particle.render.opacity = 0.1;
              particle.render.filter = "none";
            }
          } else {
            // Normal state
            particle.render.opacity = 1;
            particle.render.filter = "none";
          }
        }
      });
    }, [isPaused, hoveredWindowId, highlightedParticle]);

    // Update particle styling when hovered window, pause state, or highlighted particle changes
    useEffect(() => {
      updateParticleStyling();
    }, [updateParticleStyling]);

    // Function to pulse the ground with a particle's color
    const pulseGround = (particleColor: string) => {
      // Clear any existing timeout
      if (groundPulseTimeout) {
        clearTimeout(groundPulseTimeout);
      }

      // Update the ground's render color and border
      if (ground.current && ground.current.render) {
        ground.current.render.fillStyle = particleColor;
        ground.current.render.strokeStyle = particleColor;
        ground.current.render.lineWidth = 30;
      }

      // Animate the border over 200ms
      const startTime = Date.now();
      const duration = 200;

      const animateBorder = () => {
        const elapsed = Date.now() - startTime;
        const progress = Math.min(elapsed / duration, 1);

        // Interpolate width from 30px to 1px
        const width = 30 - progress * 29;
        // Interpolate opacity from 10% to 100%
        const opacity = 0.1 + progress * 0.9;

        if (ground.current && ground.current.render) {
          ground.current.render.lineWidth = width;
          // Set strokeStyle with opacity by using rgba
          const colorWithOpacity = hexToRgba(particleColor, opacity);
          ground.current.render.strokeStyle = colorWithOpacity;
        }

        if (progress < 1) {
          requestAnimationFrame(animateBorder);
        }
      };

      requestAnimationFrame(animateBorder);

      // Set timeout to reset the color after 200ms
      const timeout = setTimeout(() => {
        if (ground.current && ground.current.render) {
          ground.current.render.fillStyle = "#2c3e50"; // Reset to original color
          ground.current.render.strokeStyle = "#2c3e50"; // Reset border color
          ground.current.render.lineWidth = 0; // Remove border
        }
      }, 200);

      setGroundPulseTimeout(timeout);
    };

    // Store references
    const particles = useRef<Particle[]>([]);
    const attractor = useRef<Body | null>(null);
    const producers = useRef<Body[]>([]);
    const ground = useRef<Body | null>(null);
    const renderRef = useRef<Render | null>(null);
    const windowLinesCanvasRef = useRef<HTMLCanvasElement>(null);

    // Smooth animation for window lines based on particle positions
    const animateWindowLines = useCallback(() => {
      // Skip animation if paused
      if (isPaused) {
        requestAnimationFrame(animateWindowLines);
        return;
      }

      const activeWindows = windows.filter((stats) => stats.isActive);
      // Calculate line width based on number of open windows
      const lineWidth = Math.max(
        1,
        SIMULATION_CONSTANTS.ATTRACTOR_WIDTH / activeWindows.length
      ); // Minimum 1px width

      setWindowLines(() => {
        const newLines: Array<{
          id: number;
          windowId: number;
          color: string;
          x: number;
          width: number;
          isActive: boolean;
          pulseStartTime?: number;
          windowStart: number;
          windowEnd: number;
          watermarkValue: number;
          producerCounts: [number, number, number];
        }> = [];

        // Sort active windows by start time (oldest first)
        const sortedActiveWindows = activeWindows.sort(
          (a, b) => a.windowStart - b.windowStart
        );

        // Position lines at attractor edge, stacked from left to right
        let currentX = attactorPosition; // Start at attractor edge

        sortedActiveWindows.forEach((stats) => {
          // Use the window's actual windowID instead of array index
          const windowId = stats.windowID;

          newLines.push({
            id: windowId,
            windowId: windowId,
            color: getThresholdColor(windowId),
            x: currentX + lineWidth / 2, // Offset by half width to align left edge with attractor
            width: lineWidth,
            isActive: stats.isActive,
            windowStart: stats.windowStart,
            windowEnd: stats.windowEnd,
            watermarkValue: stats.watermarkValue,
            producerCounts: stats.count,
          });

          // Move to the right for the next line
          currentX += lineWidth; // No gap between lines
        });

        return newLines;
      });

      // Continue animation
      requestAnimationFrame(animateWindowLines);
    }, [windows, attactorPosition, getThresholdColor, isPaused]);

    // create a renderer
    useEffect(() => {
      let render: Render | null = null;
      let runner: Runner | null = null;
      let spawnInterval: NodeJS.Timeout | null = null;

      if (matterRef.current) {
        // Clear any existing canvas elements from previous renders
        while (matterRef.current.firstChild) {
          matterRef.current.removeChild(matterRef.current.firstChild);
        }

        // Optimize engine settings for better performance
        engine.world.gravity.y = 0.8; // Slightly reduced gravity
        engine.world.gravity.x = 0; // No horizontal gravity
        engine.timing.timeScale = 1; // Normal time scale

        render = Render.create({
          element: matterRef.current,
          engine: engine,
          options: {
            width: sceneWidth,
            height: sceneHeight,
            wireframes: false,
            background: "#f0f0f0",
            showVelocity: false, // Disable velocity visualization for performance
            showAngleIndicator: false, // Disable angle indicators for performance
          },
        });
        renderRef.current = render;

        // Canvas click handling is now done in the windowLinesCanvas click handler

        // Create ground
        ground.current = Bodies.rectangle(
          sceneWidth / 2,
          sceneHeight - 15,
          sceneWidth,
          30,
          {
            isStatic: true,
            render: { fillStyle: "#2c3e50" },
          }
        );

        // Create attractor on the right
        const attractorY = sceneHeight / 2; // Center vertically
        const attractorHeight = 540;
        attractor.current = Bodies.rectangle(
          sceneWidth - 50, // Center of 100px wide attractor (right edge flush with canvas)
          attractorY,
          100, // 100px wide
          attractorHeight,
          {
            isStatic: true,
            render: { fillStyle: "#E3E3E3" },
          }
        );

        // Create three producers on the left
        const producerPositions = [
          { x: 100, y: 150 },
          { x: 100, y: 300 },
          { x: 100, y: 450 },
        ];

        producers.current = producerPositions.map((pos, index) => {
          const producer = Bodies.circle(
            pos.x,
            pos.y,
            SIMULATION_CONSTANTS.PRODUCER_RADIUS,
            {
              isStatic: true,
              render: {
                fillStyle: hideProducers[index]
                  ? "transparent"
                  : PRODUCER_COLORS[index],
                strokeStyle: hideProducers[index] ? "transparent" : "#000",
                opacity: hideProducers[index] ? 0 : 1,
              },
            }
          );
          return producer;
        });

        // Add all static bodies to the world
        Composite.add(engine.world, [
          ground.current,
          attractor.current,
          ...producers.current,
        ]);

        // Particle spawning system
        const spawnParticle = (producerIndex: number, spawnTime?: number) => {
          // Skip spawning if simulation is paused
          if (isPausedRef.current) {
            return;
          }

          const producer = producers.current[producerIndex];
          let createdTime: number;

          // Use elapsedTime as the timer of record for all producers
          const baseTime = spawnTime || elapsedTime;

          // Different time patterns for each producer
          switch (producerIndex) {
            case 0: // Producer 1: Regular time (source of truth)
              createdTime = baseTime;
              break;
            case 1: // Producer 2: Jumbled time based on elapsed time
              const jitter1 = getLongTailJitter(); // Long-tail distribution
              createdTime = Math.max(0, baseTime - jitter1); // Clamp to minimum 0
              break;
            case 2: // Producer 3: Burst mode based on elapsed time
              const jitter2 = getLongTailJitter(); // Long-tail distribution
              createdTime = Math.max(0, baseTime - jitter2); // Clamp to minimum 0
              break;
            default:
              createdTime = baseTime;
          }

          // Event time is the same as created time (no travel time calculation)
          const eventTime = createdTime;

          // Find the window this particle belongs to based on created time
          const windowIndex = Math.floor(
            createdTime / thresholdSettingRef.current
          );

          let particle: Particle;

          const render = {
            fillStyle: getThresholdColor(windowIndex),
            strokeStyle: "rgba(0, 0, 0, 0)", // No stroke for all particles
            lineWidth: 2,
            opacity: 1,
            filter: "none",
          };

          // Different shapes for each producer
          switch (producerIndex) {
            case 0: // Producer 1: Circle
              particle = Bodies.circle(
                producer.position.x + 40,
                producer.position.y,
                particleRadius,
                {
                  restitution: 0.6,
                  friction: 0.1,
                  render,
                }
              ) as Particle;
              break;
            case 1: // Producer 2: Square
              particle = Bodies.rectangle(
                producer.position.x + 40,
                producer.position.y,
                particleRadius * 2,
                particleRadius * 2,
                {
                  restitution: 0.6,
                  friction: 0.5,
                  render,
                }
              ) as Particle;
              break;
            case 2: // Producer 3: Triangle
              particle = Bodies.polygon(
                producer.position.x + 40,
                producer.position.y,
                3, // 3 sides
                particleRadius * 1.5, // 50% bigger so it looks the same as the circle/rect particles
                {
                  restitution: 0.6,
                  friction: 0.1,
                  render,
                }
              ) as Particle;
              break;
            default:
              particle = Bodies.circle(
                producer.position.x + 40,
                producer.position.y,
                particleRadius,
                {
                  restitution: 0.6,
                  friction: 0.1,
                  render: {
                    fillStyle: getThresholdColor(windowIndex),
                    strokeStyle: "#27ae60", // Green stroke for all particles
                    lineWidth: 2,
                  },
                }
              ) as Particle;
          }

          // Add custom properties
          particle.attracted = true;
          particle.createdAt = Date.now();
          particle.sourceId = producerIndex;
          particle.createdTime = createdTime;
          particle.eventTime = eventTime; // Store when particle was supposed to reach attractor
          particle.baseTimeAtCreation = baseTime; // Store the base time for jitter calculation
          particle.windowId = windowIndex; // Assign window at creation time
          particle.thresholdAtCreation = thresholdSettingRef.current;
          particle.watermarkDelayAtCreation = watermarkDelay;

          // Note: Windows will be created on-demand when needed, not during particle creation

          particles.current.push(particle);
          Composite.add(engine.world, particle);
        };

        // Spawn particles with different patterns - optimized timing
        spawnInterval = setInterval(() => {
          // Skip spawning if simulation is paused
          if (isPausedRef.current) {
            return;
          }

          const currentTime = currentTimeRef.current;

          // Producer 1: Regular spawn every 500ms (source of truth)
          if (
            producerStatesRef.current[0] &&
            currentTime - lastSpawnTime.current[0] >= 500
          ) {
            spawnParticle(0, currentTime);
            lastSpawnTime.current[0] = currentTime;
          }

          // Producer 2: Regular spawn every 300ms (with jitter)
          if (
            producerStatesRef.current[1] &&
            currentTime - lastSpawnTime.current[1] >= 300
          ) {
            spawnParticle(1, currentTime);
            lastSpawnTime.current[1] = currentTime;
          }

          // Producer 3: Burst mode - variable burst sizes and timing
          if (
            producerStatesRef.current[2] &&
            currentTime - lastBurstTime.current > 2000
          ) {
            // Random burst size between 5-15 particles (reduced from 25)
            const burstSize = Math.floor(Math.random() * 10) + 5; // 5 to 15

            // Random timing between bursts (2-4 seconds)
            const nextBurstDelay = 2000 + Math.random() * 2000;

            // Spawn burst with variable timing - batch spawns
            const batchSize = 3; // Spawn in smaller batches
            for (let i = 0; i < burstSize; i += batchSize) {
              const batchDelay = (i / batchSize) * 100; // 100ms between batches
              const timeout = setTimeout(() => {
                for (let j = 0; j < Math.min(batchSize, burstSize - i); j++) {
                  spawnParticle(2, currentTime + batchDelay + j * 20);
                }
              }, batchDelay);
              // Track burst timeouts for pause functionality
              activeTimeouts.current.add(timeout);
            }

            lastBurstTime.current = currentTime + nextBurstDelay;
          }
        }, 100); // Less aggressive spawning every 100ms

        // Simple ground collision detection for willDrop particles
        Events.on(engine, "collisionStart", (event) => {
          event.pairs.forEach((pair) => {
            const bodyA = pair.bodyA;
            const bodyB = pair.bodyB;

            // Check if particle hits ground
            if (
              (bodyA === ground.current &&
                particles.current.includes(bodyB as Particle)) ||
              (bodyB === ground.current &&
                particles.current.includes(bodyA as Particle))
            ) {
              const particle = particles.current.includes(bodyA as Particle)
                ? (bodyA as Particle)
                : (bodyB as Particle);

              // Process ground collisions for all particles
              setDestroyedByGround((prev) => prev + 1);

              // Pulse the ground with the particle's color
              if (particle.render && particle.render.fillStyle) {
                pulseGround(particle.render.fillStyle);
              }

              // Remove from cleanup queue if it was queued for cleanup
              particlesQueuedForCleanup.current.delete(particle);

              // Remove from active particles list
              particles.current = particles.current.filter(
                (p) => p !== particle
              );

              // Add to cleanup tracking
              particlesInCleanup.current.add(particle);

              // Destroy particle after 2 seconds
              const timeout = setTimeout(() => {
                if (Composite.allBodies(engine.world).includes(particle)) {
                  Composite.remove(engine.world, particle);
                  // Remove from cleanup tracking
                  particlesInCleanup.current.delete(particle);
                }
              }, 2000);

              // Track timeout for pause functionality
              activeTimeouts.current.add(timeout);
            }
          });
        });

        // Simple particle attraction to attractor
        Events.on(engine, "beforeUpdate", () => {
          particles.current.forEach((particle) => {
            if (particle.attracted) {
              // Apply attraction force to attractor
              const attractorPos = attractor.current!.position;
              const direction = Vector.sub(attractorPos, particle.position);
              const distance = Vector.magnitude(direction);

              // Apply attraction force
              if (distance > 0) {
                const normalizedDirection = Vector.normalise(direction);
                const attractionForce = Vector.mult(
                  normalizedDirection,
                  0.0005
                );
                Body.applyForce(particle, particle.position, attractionForce);
              }

              // Dampen velocity to prevent orbital effects
              let dampedVelocity = Vector.mult(particle.velocity, 0.95);

              // Extra damping for triangles (Producer 3)
              if (particle.sourceId === 2) {
                dampedVelocity = Vector.mult(dampedVelocity, 0.8); // Additional 20% damping
              }

              // Extra damping for circles (Producer 1)
              if (particle.sourceId === 0) {
                dampedVelocity = Vector.mult(dampedVelocity, 0.8); // Additional 20% damping
              }

              // Less damping for squares (Producer 2) - 10% faster
              if (particle.sourceId === 1) {
                dampedVelocity = Vector.mult(dampedVelocity, 0.87); // Only 12% damping (vs 20%)
              }

              Body.setVelocity(particle, dampedVelocity);

              // Counteract gravity while attracted
              Body.applyForce(
                particle,
                particle.position,
                Vector.create(0, -0.00014)
              );
            }

            // Handle particles reaching the attractor area
            if (particle.attracted) {
              // Check if particle has moved close to attractor
              if (particle.position.x > attactorPosition - 20) {
                // Create window on-demand if it doesn't exist
                let window = windowsRef.current.find(
                  (w) =>
                    particle.eventTime >= w.windowStart &&
                    particle.eventTime < w.windowEnd
                );

                // If no window exists for this event, create it
                if (!window) {
                  const windowStart =
                    Math.floor(
                      particle.eventTime / thresholdSettingRef.current
                    ) * thresholdSettingRef.current;
                  const windowEnd = windowStart + thresholdSettingRef.current;
                  const windowId = Math.floor(
                    windowStart / thresholdSettingRef.current
                  );

                  const newWindow = {
                    windowID: windowId,
                    windowStart,
                    windowEnd,
                    watermarkValue: watermarkDelayRef.current,
                    count: [0, 0, 0] as [number, number, number],
                    missed: [0, 0, 0] as [number, number, number],
                    isActive: true,
                    windowSize: thresholdSettingRef.current,
                  };

                  // Add the new window to the windows array
                  setWindows((prevWindows) => {
                    const updated = [...prevWindows, newWindow];

                    // Clean up old windows to maintain max windows limit
                    const maxWindows = SIMULATION_CONSTANTS.MAX_WINDOWS;
                    if (updated.length > maxWindows) {
                      // Sort by window start time and keep the most recent windows
                      updated.sort((a, b) => a.windowStart - b.windowStart);
                      const windowsToRemove = updated.length - maxWindows;
                      updated.splice(0, windowsToRemove);
                    }

                    return updated;
                  });

                  // Update the local reference for immediate use
                  window = newWindow;
                }

                if (window && window.isActive) {
                  // Check if event was created within the valid window time
                  const eventWasCreatedInValidWindow =
                    particle.eventTime >= window.windowStart &&
                    particle.eventTime < window.windowEnd;

                  // Check if event arrives before the window's watermark deadline
                  const windowWatermarkDeadline =
                    window.windowEnd + window.watermarkValue;
                  const eventArrivesBeforeDeadline =
                    collectorTime <= windowWatermarkDeadline;

                  if (
                    eventWasCreatedInValidWindow &&
                    eventArrivesBeforeDeadline
                  ) {
                    // Event is valid - accept it
                    particle.attracted = false;
                    Composite.remove(engine.world, particle);
                    particles.current = particles.current.filter(
                      (p) => p !== particle
                    );
                    setDestroyedBySource((prev) => prev + 1);

                    // Update collector time to the latest event time received (only move forward)
                    setCollectorTime((prev) =>
                      Math.max(prev, particle.eventTime)
                    );

                    // Update window statistics - count as accepted
                    setWindows((prevWindows) => {
                      const updated = [...prevWindows];
                      const windowIndex = updated.findIndex(
                        (w) => w.windowID === window.windowID
                      );
                      if (windowIndex >= 0) {
                        const windowToUpdate = updated[windowIndex];
                        const newCount = [...windowToUpdate.count] as [
                          number,
                          number,
                          number
                        ];
                        newCount[particle.sourceId!] += 1;
                        updated[windowIndex] = {
                          ...windowToUpdate,
                          count: newCount,
                        };
                      }
                      return updated;
                    });
                  } else {
                    // Event arrives after watermark deadline - reject it (late event)

                    particle.attracted = false;
                    // Remove all horizontal momentum when dropping
                    Body.setVelocity(
                      particle,
                      Vector.create(0, particle.velocity.y)
                    );
                    // Mark for cleanup after 2000ms
                    particle.cleanupTime = Date.now() + 2000;
                    particlesQueuedForCleanup.current.add(particle);
                    setDestroyedByGround((prev) => prev + 1);

                    // Update collector time even for rejected events (only move forward)
                    setCollectorTime((prev) =>
                      Math.max(prev, particle.eventTime)
                    );

                    // Update window statistics - count as missed and close window
                    updateMissedCount(window.windowID, particle.sourceId!);
                    setWindows((prevWindows) => {
                      const updated = [...prevWindows];
                      const windowIndex = updated.findIndex(
                        (w) => w.windowID === window.windowID
                      );
                      if (windowIndex >= 0) {
                        updated[windowIndex] = {
                          ...updated[windowIndex],
                          isActive: false, // Close window when late event arrives
                        };
                      }
                      return updated;
                    });
                  }
                } else if (window && !window.isActive) {
                  // Window found but is closed - reject it (late event)

                  particle.attracted = false;
                  // Remove all horizontal momentum when dropping
                  Body.setVelocity(
                    particle,
                    Vector.create(0, particle.velocity.y)
                  );
                  // Mark for cleanup after 2000ms
                  particle.cleanupTime = Date.now() + 2000;
                  particlesQueuedForCleanup.current.add(particle);
                  setDestroyedByGround((prev) => prev + 1);

                  // Update collector time even for rejected events (only move forward)
                  setCollectorTime((prev) =>
                    Math.max(prev, particle.eventTime)
                  );

                  // Update window statistics - count as missed for closed window
                  updateMissedCount(window.windowID, particle.sourceId!);
                } else {
                  // No window found for this event - reject it

                  particle.attracted = false;
                  // Remove all horizontal momentum when dropping
                  Body.setVelocity(
                    particle,
                    Vector.create(0, particle.velocity.y)
                  );
                  // Mark for cleanup after 2000ms
                  particle.cleanupTime = Date.now() + 2000;
                  particlesQueuedForCleanup.current.add(particle);
                  setDestroyedByGround((prev) => prev + 1);

                  // Update collector time even for rejected events (only move forward)
                  setCollectorTime((prev) =>
                    Math.max(prev, particle.eventTime)
                  );

                  // For events with no window, we need to find the correct window based on event time
                  const eventWindowId = Math.floor(
                    particle.eventTime / thresholdSettingRef.current
                  );
                  updateMissedCount(eventWindowId, particle.sourceId!);
                }
              }
            }
          });
        });

        Render.run(render);

        // Create runner with optimized settings for smooth performance
        runner = Runner.create({
          maxUpdates: 60, // Reduced from 1000 to prevent excessive updates
          delta: 16.666, // Target 60 FPS (1000ms / 60fps)
        });
        Runner.run(runner, engine);

        // Cleanup
        return () => {
          if (spawnInterval) {
            clearInterval(spawnInterval);
          }
          if (runner) {
            Runner.stop(runner);
          }
          if (render) {
            Render.stop(render);
          }

          // Clear all particles
          particles.current.forEach((particle) => {
            Composite.remove(engine.world, particle);
          });
          particles.current = [];

          // Clear all static bodies
          if (ground.current) {
            Composite.remove(engine.world, ground.current);
            ground.current = null;
          }
          if (attractor.current) {
            Composite.remove(engine.world, attractor.current);
            attractor.current = null;
          }
          producers.current.forEach((producer) => {
            Composite.remove(engine.world, producer);
          });
          producers.current = [];
        };
      }
    }, [engine]);

    // Handle responsive changes without recreating the engine
    useEffect(() => {
      if (matterRef.current && renderRef.current) {
        // Update render options for responsive width
        renderRef.current.options.width = sceneWidth;
        renderRef.current.canvas.width = sceneWidth;

        // Update ground position and size
        if (ground.current) {
          Body.setPosition(ground.current, {
            x: sceneWidth / 2,
            y: sceneHeight - 15,
          });
          Body.setVertices(ground.current, [
            { x: -sceneWidth / 2, y: -15 },
            { x: sceneWidth / 2, y: -15 },
            { x: sceneWidth / 2, y: 15 },
            { x: -sceneWidth / 2, y: 15 },
          ]);
        }

        // Update attractor position to stay on the right side
        if (attractor.current) {
          Body.setPosition(attractor.current, {
            x: sceneWidth - 50, // Center of 100px wide attractor (right edge flush with canvas)
            y: sceneHeight / 2,
          });
        }
      }
    }, [sceneWidth, sceneHeight]);

    // Render window lines on canvas
    useEffect(() => {
      const canvas = windowLinesCanvasRef.current;
      if (canvas) {
        const ctx = canvas.getContext("2d");
        if (ctx) {
          // Clear canvas
          ctx.clearRect(0, 0, sceneWidth, sceneHeight);

          // Only draw window lines if not hidden
          if (!hideWindows) {
            // Draw window lines
            windowLines.forEach((line) => {
              let strokeStyle = line.color;
              let lineWidth = line.width;

              // Calculate line height based on particle count
              // Start at 100px, grow with each particle, max at 540px (attractor height)
              const baseHeight = 100;
              const maxHeight = 540;
              const heightPerParticle = 20; // 20px growth per particle
              const totalParticles = line.producerCounts.reduce(
                (sum, count) => sum + count,
                0
              );
              const lineHeight = Math.min(
                baseHeight + totalParticles * heightPerParticle,
                maxHeight
              );

              // Center the line vertically
              const lineTop = (600 - lineHeight) / 2; // 600 is sceneHeight
              const lineBottom = lineTop + lineHeight;

              // Apply pulse animation if this line is pulsing
              if (line.pulseStartTime && pulsingWindowIds.has(line.windowId)) {
                const pulseElapsed = Date.now() - line.pulseStartTime;
                const pulseDuration = 1000; // 1 second pulse
                const pulseProgress = Math.min(pulseElapsed / pulseDuration, 1);

                if (pulseProgress < 1) {
                  // Pulse effect: scale width and add glow
                  const pulseScale =
                    1 + Math.sin(pulseProgress * Math.PI) * 0.5; // Scale from 1x to 1.5x
                  lineWidth = line.width * pulseScale;

                  // Add glow effect with opacity
                  const glowOpacity = Math.sin(pulseProgress * Math.PI) * 0.8;
                  const glowColor = hexToRgba(line.color, glowOpacity);

                  // Draw glow
                  ctx.strokeStyle = glowColor;
                  ctx.lineWidth = lineWidth * 1.5;
                  ctx.beginPath();
                  ctx.moveTo(line.x, lineTop);
                  ctx.lineTo(line.x, lineBottom);
                  ctx.stroke();
                } else {
                  // Remove from pulsing set when animation is complete
                  setPulsingWindowIds((prev) => {
                    const newSet = new Set(Array.from(prev));
                    newSet.delete(line.windowId);
                    return newSet;
                  });
                }
              }

              // Apply highlighting and de-emphasizing effects based on hovered window
              if (isPaused && hoveredWindowId !== null) {
                if (line.windowId === hoveredWindowId) {
                  // Highlighted window - add glow effect
                  ctx.strokeStyle = hexToRgba(line.color, 1);
                  ctx.lineWidth = lineWidth * 1.5;
                  ctx.shadowColor = "rgba(255, 255, 255, 0.8)";
                  ctx.shadowBlur = 15;
                  ctx.shadowOffsetX = 0;
                  ctx.shadowOffsetY = 0;
                } else {
                  // De-emphasized window - reduce opacity
                  ctx.strokeStyle = hexToRgba(line.color, 0.1);
                  ctx.lineWidth = lineWidth;
                  ctx.shadowColor = "transparent";
                  ctx.shadowBlur = 0;
                  ctx.shadowOffsetX = 0;
                  ctx.shadowOffsetY = 0;
                }
              } else {
                // Normal window
                ctx.strokeStyle = strokeStyle;
                ctx.lineWidth = lineWidth;
                ctx.shadowColor = "transparent";
                ctx.shadowBlur = 0;
                ctx.shadowOffsetX = 0;
                ctx.shadowOffsetY = 0;
              }

              // Draw the main line
              ctx.beginPath();
              ctx.moveTo(line.x, lineTop);
              ctx.lineTo(line.x, lineBottom);
              ctx.stroke();
            });
          }
        }
      }
    }, [windowLines, sceneWidth, sceneHeight, pulsingWindowIds, hideWindows]);

    // Update particle colors when threshold changes
    useEffect(() => {
      particles.current.forEach((particle) => {
        if (particle.render) {
          // Only update color if particle has been assigned to a window
          if (particle.windowId !== -1) {
            particle.render.fillStyle = getThresholdColor(particle.windowId);
          }
          particle.render.strokeStyle = "rgba(0, 0, 0, 0)"; // No stroke for all particles
        }
      });
    }, [thresholdSetting]);

    // Timer to track elapsed time and close old windows - optimized
    useEffect(() => {
      if (isPaused) return;
      const timer = setInterval(() => {
        setElapsedTime((prev) => {
          const newElapsedTime = prev + 50; // Reduced increment for smoother updates
          // Update currentTimeRef to match elapsedTime
          currentTimeRef.current = newElapsedTime;
          // Close windows that have passed their end time + watermark based on collector time
          setWindows((prevWindows) => {
            const updated = [...prevWindows];
            updated.forEach((window, index) => {
              const windowEndTime = window.windowEnd;
              const watermarkValue = window.watermarkValue;
              const windowActiveUntil = windowEndTime + watermarkValue;

              // Use collector time instead of elapsed time for Flink-like behavior
              if (window.isActive && collectorTime > windowActiveUntil) {
                updated[index] = { ...window, isActive: false };
              }
            });
            return updated;
          });
          return newElapsedTime;
        });
      }, 100); // Less frequent updates every 100ms
      return () => clearInterval(timer);
    }, [isPaused, collectorTime]);

    // Timer to clean up rejected particles after 2000ms - optimized
    useEffect(() => {
      if (isPaused) return;
      const cleanupTimer = setInterval(() => {
        const currentTime = Date.now();
        const particlesToRemove: Particle[] = [];

        // Check all particles queued for cleanup - batch processing
        particlesQueuedForCleanup.current.forEach((particle) => {
          if (particle.cleanupTime && currentTime >= particle.cleanupTime) {
            particlesToRemove.push(particle);
          }
        });

        // Only process if there are particles to remove
        if (particlesToRemove.length > 0) {
          // Batch remove from physics world
          Composite.remove(engine.world, particlesToRemove);

          // Batch remove from particles array
          const particleSet = new Set(particlesToRemove);
          particles.current = particles.current.filter(
            (p) => !particleSet.has(p)
          );

          // Batch remove from cleanup queue
          particlesToRemove.forEach((particle) => {
            particlesQueuedForCleanup.current.delete(particle);
          });
        }
      }, 100); // More aggressive cleanup every 100ms
      return () => clearInterval(cleanupTimer);
    }, [isPaused, engine]);

    // Start smooth animation for window lines
    useEffect(() => {
      const animationId = requestAnimationFrame(animateWindowLines);
      return () => cancelAnimationFrame(animationId);
    }, [animateWindowLines]);

    // Note: Windows keep their original boundaries to maintain historical data integrity

    // Handle mouse events for window tooltips
    // Window tooltip handling is now done in the main mouse event useEffect

    return (
      <div style={{ position: "relative", width: "100%", maxWidth: "100vw" }}>
        {/* Window Lines Canvas Overlay */}
        <canvas
          style={{
            position: "absolute",
            top: 0,
            left: "50%",
            transform: "translateX(-50%)",
            width: `${sceneWidth}px`,
            height: `${sceneHeight}px`,
            pointerEvents: "auto",
            zIndex: 1,
            cursor: "pointer",
          }}
          width={sceneWidth}
          height={sceneHeight}
          ref={windowLinesCanvasRef}
          // Mouse events handled by useEffect for tooltips
        />
        <div
          style={{
            width: `${sceneWidth}px`,
            height: `${sceneHeight}px`,
            maxWidth: "100%",
            margin: "0 auto",
          }}
          ref={matterRef}
        />

        {/* Collector Time Display */}
        <div
          style={{
            position: "absolute",
            top: "20px",
            left: `${attactorPosition - 50}px`, // Center on attractor
            transform: "translateX(-50%)",
            background: "rgba(0, 0, 0, 0.8)",
            color: "white",
            padding: "8px 12px",
            borderRadius: "4px",
            fontFamily: "monospace",
            fontSize: "14px",
            fontWeight: "bold",
            zIndex: 10,
            pointerEvents: "none",
          }}
        >
          <div style={{ marginBottom: "4px" }}>
            Current Watermark: {formatClockTime(collectorTime)}
          </div>
          <div
            style={{
              marginBottom: "4px",
              fontSize: "12px",
              fontWeight: "normal",
            }}
          >
            Window: {thresholdSetting}ms
          </div>
          <div style={{ fontSize: "12px", fontWeight: "normal" }}>
            Delay: {watermarkDelay}ms
          </div>
        </div>

        {/* Spotlight Overlays */}
        {spotlightProducer !== null && (
          <div
            style={{
              position: "absolute",
              top: 0,
              left: "50%",
              transform: "translateX(-50%)",
              width: `${sceneWidth}px`,
              height: `${sceneHeight}px`,
              background: "rgba(0, 0, 0, 0.7)",
              zIndex: 2,
              pointerEvents: "none",
              mask: `radial-gradient(circle at 100px ${
                150 + spotlightProducer * 150
              }px, transparent 50px, black 50px)`,
              WebkitMask: `radial-gradient(circle at 100px ${
                150 + spotlightProducer * 150
              }px, transparent 50px, black 50px)`,
            }}
          />
        )}

        {highlightedParticle && (
          <div
            style={{
              position: "absolute",
              top: 0,
              left: "50%",
              transform: "translateX(-50%)",
              width: `${sceneWidth}px`,
              height: `${sceneHeight}px`,
              background: "rgba(0, 0, 0, 0.7)",
              zIndex: 2,
              pointerEvents: "none",
              mask: `radial-gradient(circle at ${highlightedParticle.position.x}px ${highlightedParticle.position.y}px, transparent 30px, black 30px)`,
              WebkitMask: `radial-gradient(circle at ${highlightedParticle.position.x}px ${highlightedParticle.position.y}px, transparent 30px, black 30px)`,
            }}
          />
        )}

        {/* Controls Panel */}
        <ControlsPanel
          thresholdSetting={thresholdSetting}
          onThresholdChange={setThresholdSetting}
          onResetSimulation={resetSimulation}
          watermarkDelay={watermarkDelay}
          onwatermarkDelayChange={setwatermarkDelay}
          producerStates={producerStates}
          onProducerToggle={(index, enabled) => {
            const newStates = [...producerStates];
            newStates[index] = enabled;
            setProducerStates(newStates);
          }}
          isPaused={isPaused}
          onPauseSet={handlePauseSet}
          hideProducers={hideProducers}
          onHideProducerSet={handleHideProducerSet}
          hideWindows={hideWindows}
          onHideWindowsSet={handleHideWindowsSet}
          spotlightProducer={spotlightProducer}
          onSpotlightProducerSet={handleSpotlightProducerSet}
          onHighlightClosestParticle={handleHighlightClosestParticle}
          onSetWindowSizeTo100000={handleSetWindowSizeTo100000}
          elapsedTime={elapsedTime}
          windowWidth={windowWidth}
          showControlsPanel={showControlsPanel}
          onControlsPanelSet={handleControlsPanelSet}
        />
        {/* Bottom Container with Grid Layout */}
        <div
          style={{
            bottom: "10px",
            left: "10px",
            right: "10px",
            display: "grid",
            gridTemplateColumns: windowWidth <= 600 ? "1fr" : "1fr 1fr",
            gap: "10px",
            height: windowWidth <= 600 ? "400px" : "200px",
          }}
        >
          {/* First Chart Area - Table or Line Chart */}
          <div style={{ height: "100%", position: "relative" }}>
            {/* Chart Toggle Button */}
            <div
              style={{
                position: "absolute",
                top: "5px",
                left: "5px",
                zIndex: 10,
              }}
            >
              <button
                onClick={() => setShowLineChart(!showLineChart)}
                style={{
                  padding: windowWidth <= 600 ? "2px 4px" : "4px 8px",
                  fontSize: windowWidth <= 600 ? "8px" : "10px",
                  backgroundColor: showLineChart ? "#27ae60" : "#e74c3c",
                  color: "white",
                  border: "none",
                  borderRadius: "3px",
                  cursor: "pointer",
                  fontFamily: "monospace",
                }}
              >
                {showLineChart ? "Table" : "Chart"}
              </button>
            </div>

            {showLineChart ? (
              <LineChart
                data={windows}
                width={Math.min(sceneWidth * 0.6, 600)}
                height={200}
              />
            ) : (
              <div
                style={{
                  background: "rgba(0,0,0,0.8)",
                  color: "white",
                  padding: "10px",
                  borderRadius: "5px",
                  fontFamily: "monospace",
                  fontSize: windowWidth <= 600 ? "9px" : "11px",
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <div
                  style={{
                    flex: "0 0 auto",
                    borderBottom: "1px solid #444",
                    paddingBottom: "8px",
                    marginBottom: "8px",
                  }}
                >
                  <table style={{ width: "100%", borderCollapse: "collapse" }}>
                    <thead>
                      <tr>
                        <th
                          style={{
                            textAlign: "left",
                            padding: "4px",
                            width: "15%",
                          }}
                        >
                          Window ID
                        </th>
                        <th
                          style={{
                            padding: "4px",
                            width: "25%",
                          }}
                        >
                          Window
                        </th>
                        <th
                          style={{
                            textAlign: "left",
                            padding: "4px",
                            width: "20%",
                          }}
                        >
                          Count (P1/P2/P3)
                        </th>
                        <th
                          style={{
                            textAlign: "left",
                            padding: "4px",
                            width: "20%",
                          }}
                        >
                          Missed (P1/P2/P3)
                        </th>
                        <th
                          style={{
                            textAlign: "left",
                            padding: "4px",
                            width: "5%",
                          }}
                        >
                          Active
                        </th>
                      </tr>
                    </thead>
                  </table>
                </div>
                <div
                  style={{
                    flex: "1 1 auto",
                    overflowY: "auto",
                    //keep this it actually works
                    height: "200px",
                  }}
                >
                  <table style={{ width: "100%", borderCollapse: "collapse" }}>
                    <tbody>
                      {windows
                        .map((stats, index) => ({ windowNum: index, stats }))
                        .sort((a, b) => b.stats.windowEnd - a.stats.windowEnd) // Sort by window end time descending
                        .map(({ windowNum, stats }) => (
                          <tr
                            key={windowNum.toString()}
                            style={{ borderBottom: "1px solid #333" }}
                          >
                            <td style={{ padding: "4px", width: "15%" }}>
                              <div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                }}
                              >
                                <div
                                  style={{
                                    width: "12px",
                                    height: "12px",
                                    backgroundColor:
                                      getThresholdColor(windowNum),
                                    marginRight: "8px",
                                    borderRadius: "2px",
                                  }}
                                />
                                {windowNum}
                              </div>
                            </td>
                            <td style={{ padding: "4px", width: "25%" }}>
                              {formatClockTime(stats.windowStart)}-
                              {formatClockTime(stats.windowEnd)}
                            </td>
                            <td style={{ padding: "4px", width: "20%" }}>
                              {stats.count.join("/")}
                            </td>
                            <td style={{ padding: "4px", width: "20%" }}>
                              {stats.missed.join("/")}
                            </td>
                            <td style={{ padding: "4px", width: "5%" }}>
                              {stats.isActive ? (
                                <span style={{ color: "#2ecc71" }}>✓</span>
                              ) : (
                                <span style={{ color: "#e74c3c" }}>✗</span>
                              )}
                            </td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}
          </div>

          {/* Second Chart Area - Tabbed Reports */}
          <div
            style={{
              background: "rgba(0,0,0,0.8)",
              color: "white",
              padding: "10px",
              borderRadius: "5px",
              fontFamily: "monospace",
              fontSize: windowWidth <= 600 ? "10px" : "12px",
              height: "100%",
              display: "flex",
              flexDirection: "column",
            }}
          >
            {/* Tab Headers */}
            <div style={{ display: "flex", marginBottom: "10px" }}>
              <button
                onClick={() => setActiveTab("operator")}
                style={{
                  padding: windowWidth <= 600 ? "6px 12px" : "8px 16px",
                  fontSize: windowWidth <= 600 ? "10px" : "12px",
                  backgroundColor:
                    activeTab === "operator" ? "#27ae60" : "#444",
                  color: "white",
                  border: "none",
                  borderRadius: "3px 3px 0 0",
                  cursor: "pointer",
                  marginRight: "2px",
                }}
              >
                Operator
              </button>
              <button
                onClick={() => setActiveTab("analyst")}
                style={{
                  padding: windowWidth <= 600 ? "6px 12px" : "8px 16px",
                  fontSize: windowWidth <= 600 ? "10px" : "12px",
                  backgroundColor: activeTab === "analyst" ? "#27ae60" : "#444",
                  color: "white",
                  border: "none",
                  borderRadius: "3px 3px 0 0",
                  cursor: "pointer",
                  marginRight: "2px",
                }}
              >
                Analyst
              </button>
              <button
                onClick={() => setActiveTab("debug")}
                style={{
                  padding: windowWidth <= 600 ? "6px 12px" : "8px 16px",
                  fontSize: windowWidth <= 600 ? "10px" : "12px",
                  backgroundColor: activeTab === "debug" ? "#27ae60" : "#444",
                  color: "white",
                  border: "none",
                  borderRadius: "3px 3px 0 0",
                  cursor: "pointer",
                }}
              >
                Debug
              </button>
            </div>

            {/* Tab Content */}
            <div style={{ flex: 1, overflowY: "auto" }}>
              {activeTab === "operator" ? (
                <OperatorReport
                  windows={windows}
                  producerStates={producerStates}
                  elapsedTime={elapsedTime}
                  windowWidth={windowWidth}
                />
              ) : activeTab === "analyst" ? (
                <AnalystReport windows={windows} windowWidth={windowWidth} />
              ) : (
                <DebugReport
                  elapsedTime={elapsedTime}
                  destroyedBySource={destroyedBySource}
                  destroyedByGround={destroyedByGround}
                  particles={particles}
                  windows={windows}
                  thresholdSetting={thresholdSetting}
                  watermarkDelay={watermarkDelay}
                  windowWidth={windowWidth}
                  resetCount={resetCount}
                  lastResetTime={lastResetTime}
                />
              )}
            </div>
          </div>
        </div>

        {/* Particle Tooltip - only shows when paused */}
        <ParticleTooltip
          show={tooltipData.show && isPaused}
          x={tooltipData.x}
          y={tooltipData.y}
          particle={tooltipData.particle}
          windowWidth={windowWidth}
          windowSize={thresholdSetting}
          hideWindows={hideWindows}
        />

        {/* Window Tooltip */}
        <WindowTooltip
          show={windowTooltipData.show && isPaused}
          x={windowTooltipData.x}
          y={windowTooltipData.y}
          windowId={windowTooltipData.windowId}
          windowStart={windowTooltipData.windowStart}
          windowEnd={windowTooltipData.windowEnd}
          watermarkValue={windowTooltipData.watermarkValue}
          producerCounts={windowTooltipData.producerCounts}
          windowWidth={windowWidth}
          windowSize={thresholdSetting}
          hideWindows={hideWindows}
        />

        {/* Producer Tooltip */}
        <ProducerTooltip
          show={producerTooltipData.show && isPaused}
          x={producerTooltipData.x}
          y={producerTooltipData.y}
          producerIndex={producerTooltipData.producerIndex}
          totalProduced={producerTooltipData.totalProduced}
          totalLost={producerTooltipData.totalLost}
          windowWidth={windowWidth}
        />
      </div>
    );
  }
);

MatterCanvas.displayName = "MatterCanvas";

export default MatterCanvas;
