import React from "react";
import { WindowStats } from "../../types";
import { PRODUCER_COLORS } from "../../utils/constants";

interface OperatorReportProps {
  windows: WindowStats[];
  producerStates: boolean[];
  elapsedTime: number;
  windowWidth: number;
}

const OperatorReport: React.FC<OperatorReportProps> = ({
  windows,
  producerStates,
  elapsedTime,
  windowWidth,
}) => {
  // Calculate producer status and statistics
  const producerAnalysis = [0, 1, 2].map((producerIndex) => {
    // Get all closed windows
    const closedWindows = windows.filter((stats) => !stats.isActive);

    let isDown = false;
    let lastReceivedTime = null;

    if (closedWindows.length > 0) {
      // Sort by window end time to find the most recent closed window
      const sortedClosedWindows = closedWindows.sort(
        (a, b) => b.windowEnd - a.windowEnd
      );
      const mostRecentClosedWindow = sortedClosedWindows[0];

      // Producer is down if the most recent closed window has zero count
      isDown = mostRecentClosedWindow.count[producerIndex] === 0;

      // Find last time we received particles from this producer
      const windowsWithData = sortedClosedWindows.filter(
        (stats) => stats.count[producerIndex] > 0
      );
      if (windowsWithData.length > 0) {
        lastReceivedTime = windowsWithData[0].windowEnd;
      }
    }

    // Calculate percentiles from closed windows with data
    const windowsWithData = closedWindows.filter(
      (stats) => stats.count[producerIndex] > 0
    );
    const counts = windowsWithData
      .map((stats) => stats.count[producerIndex])
      .sort((a, b) => a - b);
    const p25 =
      counts.length > 0 ? counts[Math.floor(counts.length * 0.25)] : 0;
    const median =
      counts.length > 0 ? counts[Math.floor(counts.length * 0.5)] : 0;
    const p75 =
      counts.length > 0 ? counts[Math.floor(counts.length * 0.75)] : 0;

    // Count zero windows
    const zeroWindows = closedWindows.filter(
      (stats) => stats.count[producerIndex] === 0
    );

    return {
      producerIndex,
      isDown,
      lastReceivedTime,
      p25,
      median,
      p75,
      totalWindows: closedWindows.length,
      zeroWindows: zeroWindows.length,
    };
  });

  return (
    <div style={{ fontSize: windowWidth <= 600 ? "9px" : "11px" }}>
      <h3
        style={{
          margin: "0 0 10px 0",
          color: "#27ae60",
          fontSize: windowWidth <= 600 ? "10px" : "inherit",
        }}
      >
        Producer Status Report
      </h3>

      {producerAnalysis.map((analysis) => (
        <div key={analysis.producerIndex} style={{ marginBottom: "15px" }}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginBottom: "5px",
              color: PRODUCER_COLORS[analysis.producerIndex],
            }}
          >
            <div
              style={{
                width: "12px",
                height: "12px",
                backgroundColor: PRODUCER_COLORS[analysis.producerIndex],
                marginRight: "8px",
                borderRadius: "2px",
              }}
            />
            <strong>Producer {analysis.producerIndex + 1}</strong>
          </div>

          <div
            style={{
              marginLeft: "20px",
              fontSize: windowWidth <= 600 ? "8px" : "10px",
            }}
          >
            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Status: </span>
              <span style={{ color: analysis.isDown ? "#e74c3c" : "#27ae60" }}>
                {analysis.isDown ? "DOWN" : "ACTIVE"}
              </span>
            </div>

            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Last received: </span>
              {analysis.isDown && analysis.lastReceivedTime ? (
                <span>{(analysis.lastReceivedTime / 1000).toFixed(1)}s</span>
              ) : (
                "N/A"
              )}
            </div>

            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Percentiles: </span>
              <span>
                25th: {analysis.p25} | Median: {analysis.median} | 75th:{" "}
                {analysis.p75}
              </span>
            </div>

            <div style={{ marginBottom: "3px" }}>
              <span style={{ color: "#aaa" }}>Windows: </span>
              <span>
                {analysis.totalWindows} total, {analysis.zeroWindows} with zero
                count
              </span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default OperatorReport;
