import React, { Component, ErrorInfo, ReactNode } from "react";

interface Props {
  children: ReactNode;
}

interface State {
  hasError: boolean;
}

class ErrorBoundary extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error: Error): State {
    // Check if it's a ResizeObserver error
    if (error.message && error.message.includes("ResizeObserver")) {
      // Don't update state for ResizeObserver errors, just suppress them
      return { hasError: false };
    }
    // Update state so the next render will show the fallback UI
    return { hasError: true };
  }

  componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    // Check if it's a ResizeObserver error
    if (error.message && error.message.includes("ResizeObserver")) {
      // Suppress ResizeObserver errors - they're harmless
      console.warn("ResizeObserver error suppressed:", error.message);
      return;
    }

    // Log other errors
    console.error("ErrorBoundary caught an error:", error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      return (
        <div
          style={{
            padding: "20px",
            color: "white",
            backgroundColor: "#333",
            textAlign: "center",
          }}
        >
          <h2>Something went wrong.</h2>
          <p>Please refresh the page to try again.</p>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;
