import React from "react";
import { formatClockTime } from "../../utils/calculations";

interface WindowTooltipProps {
  show: boolean;
  x: number;
  y: number;
  windowId: number;
  windowStart: number;
  windowEnd: number;
  watermarkValue: number;
  producerCounts: [number, number, number];
  windowWidth: number;
  windowSize: number;
  hideWindows: boolean;
}

const WindowTooltip: React.FC<WindowTooltipProps> = ({
  show,
  x,
  y,
  windowId,
  windowStart,
  windowEnd,
  watermarkValue,
  producerCounts,
  windowWidth,
  windowSize,
  hideWindows,
}) => {
  if (!show) return null;

  return (
    <div
      style={{
        position: "fixed",
        left: x + 10,
        top: y - 10,
        backgroundColor: "rgba(0, 0, 0, 0.9)",
        color: "white",
        padding: "8px 12px",
        borderRadius: "6px",
        fontSize: windowWidth <= 600 ? "10px" : "12px",
        fontFamily: "monospace",
        zIndex: 1000,
        pointerEvents: "none",
        whiteSpace: "nowrap",
        boxShadow: "0 2px 8px rgba(0, 0, 0, 0.3)",
        border: "1px solid rgba(255, 255, 255, 0.2)",
      }}
    >
      {windowSize !== 100000 && !hideWindows && (
        <div style={{ fontWeight: "bold", marginBottom: "4px" }}>
          Window ID {windowId}
        </div>
      )}
      <div>Start: {formatClockTime(windowStart)}</div>
      <div>End: {formatClockTime(windowEnd)}</div>
      <div
        style={{
          marginTop: "8px",
          borderTop: "1px solid rgba(255,255,255,0.3)",
          paddingTop: "8px",
        }}
      >
        <div style={{ fontWeight: "bold", marginBottom: "4px" }}>
          Producer Counts:
        </div>
        <div>P1: {producerCounts[0]}</div>
        <div>P2: {producerCounts[1]}</div>
        <div>P3: {producerCounts[2]}</div>
      </div>
    </div>
  );
};

export default WindowTooltip;
