import React from "react";
import { Particle } from "../../types";
import { formatTime, formatClockTime } from "../../utils/calculations";

interface ParticleTooltipProps {
  show: boolean;
  x: number;
  y: number;
  particle: Particle | null;
  windowWidth: number;
  windowSize: number;
  hideWindows: boolean;
}

const ParticleTooltip: React.FC<ParticleTooltipProps> = ({
  show,
  x,
  y,
  particle,
  windowWidth,
  windowSize,
  hideWindows,
}) => {
  if (!show || !particle) return null;

  const producerNames = [
    "Producer 1 (Regular Time)",
    "Producer 2 (Jumbled Time)",
    "Producer 3 (Burst Mode)",
  ];
  const producerName = producerNames[particle.sourceId || 0];
  const creationTime = particle.createdTime;
  const windowStart = particle.windowId * particle.thresholdAtCreation;
  const windowEnd = windowStart + particle.thresholdAtCreation;
  const watermarkValue = particle.watermarkDelayAtCreation;

  // Calculate delay for Producer 2 and 3 (jumbled time and burst mode)
  let delayIndicator = null;
  if (
    (particle.sourceId === 2 || particle.sourceId === 3) &&
    particle.baseTimeAtCreation !== undefined
  ) {
    const expectedTime = particle.baseTimeAtCreation;
    const delay = Math.round(creationTime - expectedTime); // How late the event was emitted

    // Only show delay if the event was actually late (delay > 0)
    if (delay > 0) {
      let delayColor = "#ff0000"; // Red for > 5000ms
      if (delay <= 500) {
        delayColor = "#00ff00"; // Green for ≤ 500ms
      } else if (delay <= 5000) {
        delayColor = "#ffa500"; // Orange for ≤ 5000ms
      }

      delayIndicator = (
        <span style={{ color: delayColor }}>({formatTime(delay)} late)</span>
      );
    }
  }

  return (
    <div
      style={{
        position: "fixed",
        left: x + 10,
        top: y - 10,
        backgroundColor: "rgba(0, 0, 0, 0.9)",
        color: "white",
        padding: "8px 12px",
        borderRadius: "6px",
        fontSize: windowWidth <= 600 ? "10px" : "12px",
        fontFamily: "monospace",
        textAlign: "left",
        zIndex: 1000,
        pointerEvents: "none",
        whiteSpace: "nowrap",
        boxShadow: "0 2px 8px rgba(0, 0, 0, 0.3)",
        border: "1px solid rgba(255, 255, 255, 0.2)",
      }}
    >
      {producerName}
      <hr></hr>
      <div>
        Event timestamp: {formatClockTime(creationTime)} {delayIndicator}
      </div>
      {windowSize !== 100000 && !hideWindows && (
        <>
          <hr></hr>
          <div>
            Window ID {particle.windowId} ({formatClockTime(windowStart)} -{" "}
            {formatClockTime(windowEnd)})
          </div>
        </>
      )}
    </div>
  );
};

export default ParticleTooltip;
